.card-promo
  background: $color-gray-tertiary
  border-radius: 20px
  overflow: hidden
  min-height: 238px
  display: flex
  justify-content: stretch
  align-items: stretch
  text-decoration: none
  @include breakpoint($desktop-max-1280)
    width: 188px
    min-width: 188px
  @include breakpoint($tablet-max-1040)
    width: 188px
    min-width: 188px
    min-height: 240px
  &__container
    padding: 12px
    width: 100%
    min-height: 100%
    position: relative
    //@include breakpoint($tablet-max-1040)
    &:after
      content: ''
      display: block
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.00) 6.67%, rgba(0, 0, 0, 0.02) 13.33%, rgba(0, 0, 0, 0.03) 20%, rgba(0, 0, 0, 0.06) 26.67%, rgba(0, 0, 0, 0.09) 33.33%, rgba(0, 0, 0, 0.13) 40%, rgba(0, 0, 0, 0.18) 46.67%, rgba(0, 0, 0, 0.22) 53.33%, rgba(0, 0, 0, 0.27) 60%, rgba(0, 0, 0, 0.31) 66.67%, rgba(0, 0, 0, 0.34) 73.33%, rgba(0, 0, 0, 0.37) 80%, rgba(0, 0, 0, 0.38) 86.67%, rgba(0, 0, 0, 0.40) 93.33%, rgba(0, 0, 0, 0.40) 100%)
      width: 100%
      height: 88px
      position: absolute
      left: 0
      right: 0
      top: 0
      z-index: 2
      //@include breakpoint($tablet-max-1040)
  h2
    @include preview-promo-title
    color: $color-white-primary
    position: relative
    z-index: 3
    //@include breakpoint($tablet-max-1040)
  img
    object-fit: cover
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    //@include breakpoint($tablet-max-1040)
