.product
  @include breakpoint($tablet-max-1040)
    overflow: hidden
    border-radius: 24px 24px 0 0
  &__container
    @include breakpoint($tablet-max-1040)
      position: relative
  &__row
    display: grid
    grid-template-columns: 474px 432px
    grid-column-gap: 24px
    @include breakpoint($tablet-max-1040)
      display: flex
      flex-direction: column
      justify-content: flex-start
    @media (max-width: 900px)
      .container
        grid-template-columns: auto
  &__col
    //&--left
    &--right
      padding: 6px 0 0 0
      @include breakpoint($tablet-max-1040)
        padding: 0 20px 98px
        width: 100vw
        //position: relative
  &__carousel
    border-radius: 24px 24px 0 0
    overflow: hidden
    background: #FAFAFA
    margin: 0 0 36px
    //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__carousel-item
    width: 100%
    height: 474px
    position: relative
    margin-right: 0
    @include breakpoint($tablet-max-1040)
      height: 375px
      overflow: hidden
      border-radius: 24px 24px 0 0
  img
    object-fit: cover
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    @include breakpoint($tablet-max-1040)
      border-radius: 24px 24px 0 0
      overflow: hidden
  .flickity-prev-next-button
    background-color: transparent
    will-change: left, right
    user-select: none !important
    border: none !important
    outline: none !important
    //@include breakpoint($tablet-max-1040)
    &[disabled]
      opacity: 0
      //@include breakpoint($tablet-max-1040)
    &.previous
      @include icon-arrow-left-product
      transform-origin: center center
      border-radius: 0
      outline: none
      user-select: none
      left: 0
      transition: left 0.1s ease, opacity 0.2s ease
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        @include icon-arrow-gray-left-2
        position: absolute
        left: 50%
        top: 50%
        transform: translate3d(-50%, -50%, 0)
        transform-origin: center center
        transition: transform 0.24s ease
        //@include breakpoint($tablet-max-1040)
      &:hover:after
        transform: translate3d(calc(-50% - 2px), -50%, 0)
        //@include breakpoint($tablet-max-1040)
    &.next
      @include icon-arrow-right-product
      transform-origin: center center
      border-radius: 0
      outline: none
      user-select: none
      right: 0
      transition: right 0.1s ease, opacity 0.2s ease
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        @include icon-arrow-gray-right-2
        position: absolute
        left: 50%
        top: 50%
        transform: translate3d(-50%, -50%, 0)
        transform-origin: center center
        transition: transform 0.24s ease
        //@include breakpoint($tablet-max-1040)
      &:hover:after
        transform: translate3d(calc(-50% + 2px), -50%, 0)
        //@include breakpoint($tablet-max-1040)
    &:hover
      background-color: transparent
      //@include breakpoint($tablet-max-1040)
    &:active
      opacity: 0.84
      //@include breakpoint($tablet-max-1040)
    .flickity-button-icon
      display: none !important
      visibility: hidden !important
      opacity: 0 !important
      //@include breakpoint($tablet-max-1040)
    &:active
      opacity: 1
    //@include breakpoint($tablet-max-1040)
  .flickity-page-dots
    bottom: 14px
    //@include breakpoint($tablet-max-1040)
    .dot
      width: 8px
      height: 8px
      margin: 0 4px
    //@include breakpoint($tablet-max-1040)
  .flickity-viewport
    @include breakpoint($tablet-max-1040)
      overflow: hidden
      border-radius: 24px 24px 0 0
  &__similar-products
    &--desktop
      display: block
      @include breakpoint($tablet-max-1040)
        display: none
    &--mobile
      display: none
      @include breakpoint($tablet-max-1040)
        display: block
        margin: 8px 0 0 0
  &__similar-products-title
    font: 600 20px/1.2 $font-rubik
    color: $color-black-secondary
    letter-spacing: -0.1px
    margin: 0 0 24px
    //@include breakpoint($tablet-max-1040)
  &__similar-products-container
    display: grid
    grid-template-columns: repeat(2, 1fr)
    grid-template-rows: repeat(3, auto)
    grid-column-gap: 14px
    grid-row-gap: 24px
    &::-webkit-scrollbar
      display: none
    @include breakpoint($tablet-max-1040)
      display: flex
      flex-wrap: nowrap
      column-gap: 0
      row-gap: 0
      overflow-x: scroll
      scrollbar-width: none
      //@include breakpoint($tablet-max-1040)
      &::-webkit-scrollbar
        display: none
  &__header
    margin: 0 0 20px
    padding: 0 38px 0 0
    cursor: pointer
    @include breakpoint($tablet-max-1040)
      margin: 0 0 16px
      padding: 0 0 0 0
  &__title
    @include product-header
    color: $color-gray-quaternary
    //@include breakpoint($tablet-max-1040)
  &__weight
    @include product-subheader
    color: $color-gray-secondary
    //@include breakpoint($tablet-max-1040)
  &__btn
    &--close
      @include icon-close-big($color-gray-tertiary)
      position: absolute
      right: 0
      top: 0
      transition: background-image 0.24s ease
      @include breakpoint($tablet-max-1040)
        @include icon-close-big($color-white-primary)
        right: 20px
        top: 20px
      &:hover
        @include icon-close-big($color-gray-quinary)
        //@include breakpoint($tablet-max-1040)
    &--calc
      @include btn-red
      display: flex
      justify-content: center
      align-items: center
      column-gap: 12px
      padding: 14px 24px
      @include breakpoint($tablet-max-1040)
        border-radius: 0
        font-size: 18px
        font-weight: 500
      &.product__btn--active
        column-gap: 24px
        //@include breakpoint($tablet-max-1040)
  &__parameters
    padding: 16px 0
    @include breakpoint($tablet-max-1040)
      border-top: 1px solid $color-gray-tertiary
  &__parameters-title
    font: 600 14px/1.28 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-secondary
    margin: 0 0 8px
    @include breakpoint($tablet-max-1040)
      font: 500 13px/1.38 $font-rubik
      color: $color-gray-secondary
      letter-spacing: 0.02px
  &__parameters-row
    display: grid
    grid-template-columns: repeat(auto-fill, minmax(110px, 1fr))
    gap: 8px
    //@include breakpoint($tablet-max-1040)
  //&__parameters-col
  &__parameters-subtitle
    font: 600 20px/1.2 $font-rubik
    letter-spacing: -0.32px
    color: $color-gray-primary
    @include breakpoint($tablet-max-1040)
      font: 500 18px/1.22 $font-rubik
      color: $color-gray-primary
      letter-spacing: -0.38px
  &__parameters-text
    font: 600 14px/1.28 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-secondary
    @include breakpoint($tablet-max-1040)
      font: 500 18px/1.38 $font-rubik
      letter-spacing: -0.1px
  &__section
    margin: 0 0 16px
    @include breakpoint($tablet-max-1040)
      margin: 0
      padding: 16px 0
      border-top: 1px solid $color-gray-tertiary
    &--indent-vertical-big
      margin: 40px 0 32px
      //@include breakpoint($tablet-max-1040)
    &--price-desktop
      display: block
      @include breakpoint($tablet-max-1040)
        display: none
    &--price-mobile
      display: none
      width: calc(100% + 40px)
      transform: translate3d(-20px,0,0)
      padding: 0
      margin: 0
      position: absolute
      left: 0
      right: 0
      bottom: 0
      z-index: 3
      @include breakpoint($tablet-max-1040)
        display: block
    &:last-child
      margin-bottom: 0
      @include breakpoint($tablet-max-1040)
        border-bottom: none
  &__section-title
    @include product-subheading
    color: $color-gray-secondary
    margin: 0 0 6px
    //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__section-text
    @include product-subdescr
    color: $color-gray-primary
    //@include breakpoint($tablet-max-1040)
    &--hidden
      cursor: pointer
      height: 90px
      overflow: hidden
      position: relative
      //@include breakpoint($tablet-max-1040)
      &:before
        content: ''
        @include icon-arrow-bottom-with-bg
        position: absolute
        right: 0
        bottom: 0
        z-index: 2
        transition: opacity 0.24s ease
        //@include breakpoint($tablet-max-1040)
      &:hover:before
        opacity: 0.84
        //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        display: block
        width: 100%
        height: 20px
        background: linear-gradient(0deg, rgb(255, 255, 255) 4%, rgba(255, 255, 255, 0.6418942577) 100%)
        position: absolute
        left: 0
        right: 0
        bottom: 0
        z-index: 1
        //@include breakpoint($tablet-max-1040)
  &__gallery-wrapper
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 9998
    //@include breakpoint($tablet-max-1040)
  &__gallery
    width: 100%
    position: absolute !important
    left: 50% !important
    top: 50% !important
    transform: translate3d(-50%,-50%,0) !important
    //@include breakpoint($tablet-max-1040)
    .flickity-prev-next-button
      background-color: transparent
      will-change: left, right
      user-select: none
      cursor: pointer
      //@include breakpoint($tablet-max-1040)
      &[disabled]
        opacity: 0
        //@include breakpoint($tablet-max-1040)
      &.previous
        @include icon-arrow-white-left-big
        transform-origin: center center
        border-radius: 0
        outline: none
        user-select: none
        left: 72px
        transition: left 0.1s ease, opacity 0.2s ease
        @include breakpoint($tablet-max-1040)
          left: 24px
        &:hover
          left: 70px
          @include breakpoint($tablet-max-1040)
            left: 24px
      &.next
        @include icon-arrow-white-right-big
        transform-origin: center center
        border-radius: 0
        outline: none
        user-select: none
        right: 72px
        transition: right 0.1s ease, opacity 0.2s ease
        @include breakpoint($tablet-max-1040)
          right: 24px
        &:hover
          right: 68px
          @include breakpoint($tablet-max-1040)
            right: 24px
      &:hover
        background-color: transparent
        //@include breakpoint($tablet-max-1040)
      &:active
        opacity: 0.84
        //@include breakpoint($tablet-max-1040)
      .flickity-button-icon
        display: none !important
        visibility: hidden !important
        opacity: 0 !important
        //@include breakpoint($tablet-max-1040)
      &:active
        opacity: 1
        //@include breakpoint($tablet-max-1040)
    .flickity-prev-next-button
      @include breakpoint($tablet-max-1040)
        top: calc(50% + 27px)
  &__gallery-item
    width: 100%
    height: 64.814814814814815vh
    position: relative
    margin-right: 0
    //@include breakpoint($tablet-max-1040)
    img
      object-fit: cover
      width: auto
      height: 100%
      position: absolute
      left: 50%
      transform: translate3d(-50%,0,0) scale(1.1)
      top: 0
      transform-origin: center center
      bottom: 0
      z-index: 1
      @include breakpoint($tablet-max-1040)
        object-fit: none
        width: 100%
        height: auto
        position: relative
        left: auto
        transform: translate3d(0, 0, 0) scale(1)
        top: 0
        transform-origin: center center
        bottom: 0
        z-index: 1
  &__gallery-nav
    width: 100%
    position: absolute !important
    left: 50% !important
    bottom: 32px !important
    top: auto !important
    transform: translate3d(-50%, 0, 0) !important
    @include breakpoint($tablet-max-1040)
      display: none
  &__gallery-nav-item
    width: 80px
    margin-right: 40px
    border: 1px solid transparent
    transition: border 0.24s ease
    //@include breakpoint($tablet-max-1040)
    &.is-selected
      border: 1px solid $color-red-primary
      //@include breakpoint($tablet-max-1040)
    img
      display: block
      width: 100%
      height: auto
      //@include breakpoint($tablet-max-1040)
  &__btn-ctrl
    width: 50%
    position: relative
    //@include breakpoint($tablet-max-1040)
    &--minus
      @include icon-minus($color-white-primary)
      display: none
      @include breakpoint($tablet-max-1040)
        width: 17px
        min-width: 17px
      .product__btn--active &
        display: block
        //@include breakpoint($tablet-max-1040)
    &--plus
      @include icon-plus($color-white-primary)
      @include breakpoint($tablet-max-1040)
        width: 17px
        min-width: 17px
        height: 17px
        min-height: 17px
  //&__btn-text
