.error
  height: 100vh
  background: $color-white-primary
  //@include breakpoint($tablet-max-1040)
  &__container
    @include glass(960px)
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    height: 100%
    //@include breakpoint($tablet-max-1040)
  .icon
    margin: 0 0 20px
    //@include breakpoint($tablet-max-1040)
  &__title
    font: 600 18px/1.88 $font-rubik
    color: $color-black-secondary
    letter-spacing: -0.38px
    margin: 0 0 12px
    text-align: center
    //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__text
    font: 400 15px/1.2 $font-rubik
    color: $color-black-secondary
    letter-spacing: -0.05px
    margin: 0 0 30px
    text-align: center
    //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  .button
    width: auto
    min-width: 304px
    //@include breakpoint($tablet-max-1040)
