.tag
  display: flex
  justify-content: center
  align-items: center
  min-height: 36px
  padding: 11px 16px
  background-color: $color-gray-tertiary
  border-radius: 40px
  font: 600 14px/1 $font-rubik
  letter-spacing: -.1px
  color: $color-gray-primary
  text-decoration: none
  transition: background 0.24s ease, color 0.24s ease
  @include breakpoint($tablet-max-1040)
    padding: 10px 14px
    font-weight: 500
  &:hover
    color: $color-gray-primary
    background: $color-gray-quinary
    //@include breakpoint($tablet-max-1040)
