.notice
  width: 100%
  max-width: 328px
  border-radius: 20px
  background: $color-white-primary
  box-shadow: 0 3px 20px 0 rgba($color-black-secondary, 0.08)
  //@include breakpoint($tablet-max-1040)
  &__container
    padding: 13px 20px
    display: flex
    justify-content: flex-start
    align-items: center
    column-gap: 20px
    @include breakpoint($tablet-max-1040)
      padding: 11px 14px
  &__text
    font: 500 13px/1.23 $font-rubik
    color: $color-gray-quaternary
    letter-spacing: -0.05px
    width: 100%
    min-width: 218px
    @include breakpoint($tablet-max-1040)
      font: 300 11px/1.23 $font-rubik
      letter-spacing: -0.03px
      min-width: auto
  a
    color: $color-red-primary
    @include hover
    //@include breakpoint($tablet-max-1040)
