.preview-product
  transform-origin: center center
  transition: transform 0.2s ease-in-out
  @include breakpoint($tablet-max-1040)
    min-width: 262px
    margin-right: 22px
  &:active
    transform: scale(0.98)
    //@include breakpoint($tablet-max-1040)
  //.section__row--mobile-column &
    //@include breakpoint($tablet-max-1040)
  //    width: 50%
  //    min-width: 50%
  //  @include breakpoint($mobile-max-480)
  //    width: 100%
  //    min-width: 100%
  //    margin-right: 0
  .section__row--special-mobile-view &
    @include breakpoint($tablet-max-1040)
      min-width: auto
      margin-right: 0
  &:last-child
    @include breakpoint($tablet-max-1040)
      margin-right: 0
  &__container
    display: flex
    justify-content: flex-start
    align-items: flex-start
    flex-direction: column
    @include breakpoint($tablet-max-1040)
      flex-direction: row
      column-gap: 8px
      align-items: stretch
    .section__row--special-mobile-view &
      @include breakpoint($tablet-max-1040)
        flex-direction: column
        align-items: flex-start
  &__row
    &--image
      border-radius: 18px
      background: $color-gray-tertiary
      overflow: hidden
      margin: 0 0 6px
      width: 100%
      position: relative
      cursor: pointer
      padding-bottom: 100%
      @include breakpoint($tablet-max-1040)
        margin: 0 0 0
        width: 80px
        min-width: 80px
        height: 80px
        min-height: 80px
        padding-bottom: 0
      .section__row--special-mobile-view &
        @include breakpoint($tablet-max-1040)
          margin: 0 0 6px
          padding-bottom: 100%
          width: 100%
          min-width: 100%
          height: 100%
          min-height: 100%
    &--info
      margin: 0 0 20px 0
      display: flex
      justify-content: flex-start
      align-items: flex-start
      flex-direction: column
      @include breakpoint($tablet-max-1040)
        margin: 0 0 0
        width: 100%
      .section__row--special-mobile-view &
        @include breakpoint($tablet-max-1040)
          margin: 0 0 20px 0
    &--controls
      display: grid
      grid-template-columns: 25px auto
      grid-column-gap: 6px
      align-items: center
      width: 100%
      @include breakpoint($tablet-max-1040)
        margin-top: auto
        width: 100%
        justify-content: flex-start
    &--desktop
      display: flex
      @include breakpoint($tablet-max-1040)
        display: none
    &--mobile
      display: none
      align-items: center
      justify-content: space-between
      @include breakpoint($tablet-max-1040)
        display: flex
  &__quantity
    display: flex
    justify-content: center
    align-items: center
    width: 100%
    height: 100%
    background: rgba($color-gray-quaternary,29.8%)
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    font: 600 32px/1 $font-rubik
    color: $color-white-primary
    letter-spacing: -0.6px
    z-index: 3
    visibility: hidden
    opacity: 0
    transition: visibility 0s linear 0.2s, opacity 0.2s linear
    @include breakpoint($tablet-max-1040)
      left: auto
      right: auto
      top: auto
      bottom: auto
      position: relative
      font-size: 22px
      letter-spacing: -0.4px
    .section__row--special-mobile-view &
      @include breakpoint($tablet-max-1040)
        position: absolute
    &--active
      visibility: visible
      opacity: 1
      transition-delay: 0s, 0s
      animation: animateText 0.5s ease-in-out 0s 1
      //@include breakpoint($tablet-max-1040)
  &__discount-label
    @include sale-small
    color: $color-white-primary
    display: flex
    min-height: 15px
    padding: 1px 4.61px 1px 5px
    align-items: center
    border-radius: 24px
    background: $color-green-primary
    position: absolute
    left: 8px
    top: 8px
    z-index: 2
    //@include breakpoint($tablet-max-1040)
  &__img
    object-fit: cover
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    //@include breakpoint($tablet-max-1040)
  &__title
    @include preview-product-title
    color: $color-gray-primary
    margin: 0 0 2px
    cursor: pointer
    @include breakpoint($tablet-max-1040)
      margin: 0 0 4px
  &__descr
    display: flex
    justify-content: flex-start
    align-items: center
    //@include breakpoint($tablet-max-1040)
  &__descr-text
    font: 600 13px/1.15 $font-rubik
    color: $color-gray-secondary
    letter-spacing: 0.02px
    position: relative
    @include breakpoint($tablet-max-1040)
      font-family: $font-rubik
      font-size: 11px
      font-style: normal
      font-weight: 500
      line-height: 13px
      letter-spacing: 0.02px
    &--green
      color: $color-green-primary
      display: flex
      justify-content: flex-start
      align-items: center
      //@include breakpoint($tablet-max-1040)
      &:before
        content: '·'
        font: 600 13px/1.15 $font-rubik
        color: $color-gray-secondary
        padding: 0 4px
        //@include breakpoint($tablet-max-1040)
  &__col
    cursor: default
    user-select: none
    //@include breakpoint($tablet-max-1040)
    //&--remove-product
    &--add-product
      display: grid
      grid-template-columns: 1fr 25px
      grid-column-gap: 6px
      align-items: center
      width: 100%
      //left: 50%
      //position: relative
      //transform: translate3d(-50%,0,0)
      @include breakpoint($tablet-max-1040)
        grid-template-columns: auto
        width: 100%
      .section__row--special-mobile-view &
        @include breakpoint($tablet-max-1040)
          width: auto
    &--active
      cursor: pointer
      //@include breakpoint($tablet-max-1040)
  &__ctrl
    transition: background 0.24s ease, opacity 0.24s ease
    //@include breakpoint($tablet-max-1040)
    &--minus
      @include icon-minus-circle($color-gray-senary)
      .preview-product__col--remove-product.preview-product__col--active:hover &
        opacity: 0.8
        //@include breakpoint($tablet-max-1040)
      .preview-product__col--active &
        @include icon-minus-circle($color-gray-primary)
        //@include breakpoint($tablet-max-1040)
      .preview-product--discount .preview-product__col--active &
        @include icon-minus-circle($color-green-secondary)
        //@include breakpoint($tablet-max-1040)
    &--plus
      @include icon-plus-circle($color-gray-senary)
      //@include breakpoint($tablet-max-1040)
      .preview-product__col--add-product.preview-product__col--active:hover &
        opacity: 0.8
        //@include breakpoint($tablet-max-1040)
      .preview-product__col--active &
        @include icon-plus-circle($color-gray-primary)
        //@include breakpoint($tablet-max-1040)
      .preview-product--discount &
        @include icon-plus-circle($color-green-secondary)
        //@include breakpoint($tablet-max-1040)
  &__price
    border-radius: 40px
    background: $color-gray-tertiary
    padding: 7px 8px
    min-height: 32px
    display: flex
    justify-content: center
    align-items: center
    @include breakpoint($tablet-max-1040)
      padding: 7px 10px
    .preview-product--discount &
      background: $color-green-secondary
      column-gap: 4px
      //@include breakpoint($tablet-max-1040)
    .section__row--special-mobile-view &
      @include breakpoint($tablet-max-1040)
        width: 100%
  &__price-item
    &--new
      @include preview-product-price-new
      color: $color-gray-primary
      //@include breakpoint($tablet-max-1040)
      .preview-product--discount &
        color: $color-white-primary
        //@include breakpoint($tablet-max-1040)
    &--old
      @include preview-product-price-old
      color: rgba($color-black-secondary,0.45)
      //@include breakpoint($tablet-max-1040)
@keyframes animateText
  0%
    transform: scale(1)
    //@include breakpoint($tablet-max-1040)
  50%
    transform: scale(1.1)
    //@include breakpoint($tablet-max-1040)
  100%
    transform: scale(1)
    //@include breakpoint($tablet-max-1040)
