.footer
  padding: 40px 16px 40px
  min-width: 740px
  @include breakpoint($tablet-max-1040)
    padding: 40px 0 16px
    min-width: auto
  &__container
    @include breakpoint($tablet-max-1040)
      padding-left: 12px
      padding-right: 12px
  &__row
    margin: 0 0 40px
    //@include breakpoint($tablet-max-1040)
    &--indent-bottom-small
      margin: 0 0 32px
      @include breakpoint($tablet-max-1040)
        margin: 0 0 40px
    &--space-between
      display: flex
      justify-content: space-between
      align-self: flex-start
      @include breakpoint($desktop-max-1280)
        display: flex
        justify-content: space-between
        flex-direction: column
        row-gap: 12px
      //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__col
    display: flex
    justify-content: flex-start
    align-items: flex-start
    column-gap: 12px
    //@include breakpoint($tablet-max-1040)
    &:first-child
      @include breakpoint($tablet-max-1040)
        display: none
  &__link
    transition: opacity 0.24s ease
    //@include breakpoint($tablet-max-1040)
    &--appstore
      @include icon-download-app-gallery
      //@include breakpoint($tablet-max-1040)
    &--google-play
      @include icon-download-google-play
      //@include breakpoint($tablet-max-1040)
    &--app-gallery
      @include icon-download-app-gallery
      //@include breakpoint($tablet-max-1040)
    &--rustore
      @include icon-download-rustore
      //@include breakpoint($tablet-max-1040)
    &--tiktok
      @include icon-tiktok
      //@include breakpoint($tablet-max-1040)
    &--vk
      @include icon-vk
      //@include breakpoint($tablet-max-1040)
    &--ok
      @include icon-ok
      //@include breakpoint($tablet-max-1040)
    &--youtube
      @include icon-youtube
      //@include breakpoint($tablet-max-1040)
    &:hover
      opacity: 0.5
      //@include breakpoint($tablet-max-1040)
  &__nav
    display: grid
    grid-template-columns: repeat(3, 1fr)
    @include breakpoint($desktop-max-1440)
      grid-template-columns: repeat(2, 1fr)
    @include breakpoint($tablet-max-1040)
      grid-template-columns: repeat(1, 1fr)
      row-gap: 14px
  &__nav-item
    @include footer-link
    color: $color-gray-primary
    width: 100%
    margin: 0 0 12px
    padding: 0 20px 0 0
    cursor: pointer
    transition: color 0.24s ease
    @include breakpoint($tablet-max-1040)
      margin-bottom: 0
      padding-right: 0
    &:nth-child(3n+3)
      margin-bottom: 0
      @include breakpoint($tablet-max-1040)
        margin-bottom: 0
    &:hover
      color: $color-gray-secondary
      //@include breakpoint($tablet-max-1040)
  &__text
    font: 600 13px/1.15 $font-rubik
    color: $color-gray-secondary
    letter-spacing: 0.02px
    @include breakpoint($tablet-max-1040)
      font: 600 11px/1.2 $font-rubik
      letter-spacing: 0.01px
