.add-address
  width: 100%
  //@include breakpoint($tablet-max-1040)
  &__container
    width: 100%
    position: relative
    //@include breakpoint($tablet-max-1040)
  &__row
    width: 100%
    margin: 0 0 24px
    //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
    //&--pointer
    //  cursor: pointer
  &__label
    font: 500 20px/1 $font-rubik
    color: $color-black-secondary
    letter-spacing: -0.32px
    position: relative
    display: flex
    justify-content: flex-start
    align-items: center
    @include breakpoint($tablet-max-1040)
      font-size: 18px
      letter-spacing: -0.22px
    &:before
      content: ''
      @include icon-plus($color-black-secondary)
      margin: 0 12px 0 0
      @include breakpoint($tablet-max-1040)
        width: 18px
        min-width: 18px
        height: 18px
        min-height: 18px
        margin: 0 8px 0 0
    &--small
      font: 500 12px/1.18 $font-rubik
      color: $color-gray-secondary
      letter-spacing: -0.32px
      margin: 0 0 6px
      //@include breakpoint($tablet-max-1040)
      &:before
        display: none
        //@include breakpoint($tablet-max-1040)
  &__input-text-wrapper
    position: relative
    //@include breakpoint($tablet-max-1040)
  &__input-text
    width: 100%
    position: absolute
    left: 0
    top: 0
    background: transparent
    border: none
    outline: none
    padding: 0 0 5px
    color: $color-black-secondary
    text-align: left
    font-family: $font-rubik
    font-size: 18px
    font-style: normal
    font-weight: 500
    line-height: 1
    letter-spacing: -0.32px
    z-index: 1
    transition: color 0.3s ease, border-color 0.3s ease
    //@include breakpoint($tablet-max-1040)
    @include placeholder
      color: $color-black-secondary
      transition: color 0.3s ease, border-color 0.3s ease
      //@include breakpoint($tablet-max-1040)
    .add-address__input-text-wrapper--active &
      position: relative
      border-bottom: 1px solid $color-gray-quinary
      //@include breakpoint($tablet-max-1040)
  &__btn
    //@include breakpoint($tablet-max-1040)
    &--close
      @include icon-close($color-black-secondary)
      cursor: pointer
      width: 10px
      min-width: 10px
      height: 10px
      min-height: 10px
      display: none
      position: absolute
      right: 0
      top: 50%
      transform: translate3d(0, calc(-50% - 2.5px), 0)
      z-index: 2
      //@include breakpoint($tablet-max-1040)
      .add-address__input-text-wrapper--active &
        display: block
        //@include breakpoint($tablet-max-1040)
  &__list
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: flex-start
    //@include breakpoint($tablet-max-1040)
  &__list-item
    width: 100%
    font: 400 16px/1.25 $font-rubik
    color: $color-black-secondary
    letter-spacing: -0.05px
    padding: 12px 0
    cursor: pointer
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: flex-start
    transition: color 0.24s ease
    //@include breakpoint($tablet-max-1040)
    &:hover
      color: $color-gray-primary
      //@include breakpoint($tablet-max-1040)
    span
      &:first-child
        font: 400 16px/1.25 $font-rubik
        color: $color-black-secondary
        letter-spacing: -0.05px
        //@include breakpoint($tablet-max-1040)
      &:last-child
        font: 500 12px/1.18 $font-rubik
        color: $color-gray-secondary
        letter-spacing: -0.32px
        margin: 0 0 6px
        //@include breakpoint($tablet-max-1040)
