.search
  position: relative
  @include breakpoint($tablet-max-1040)
    width: 100%
  &__container
    @include breakpoint($tablet-max-1040)
      display: flex
      justify-content: flex-start
      align-items: center
      column-gap: 10px
  &__col
    @include breakpoint($tablet-max-1040)
      position: relative
    &:first-child
      width: 100%
      .popup--mobile-search &
        @include breakpoint($tablet-max-1040)
          width: auto
    &:last-child
      display: none
      @include breakpoint($tablet-max-1040)
        display: block
      .popup--mobile-search &
        @include breakpoint($tablet-max-1040)
          width: 100%
  &__btn
    cursor: pointer
    @include hover
    //@include breakpoint($tablet-max-1040)
    &--search
      appearance: none
      user-select: none
      background-color: transparent
      outline: none
      border: none
      position: absolute
      left: 0
      top: 50%
      transform: translate3d(0,-50%,0)
      display: flex
      justify-content: center
      align-items: center
      height: 100%
      padding: 0 8px 0 20px
      @include breakpoint($tablet-max-1040)
        padding: 0 8px 0 12px
      &:before
        content: ''
        @include icon-search
        @include breakpoint($tablet-max-1040)
          width: 14px
          min-width: 14px
          height: 14px
          min-height: 14px
    &--close
      position: absolute
      right: 0
      top: 50%
      transform: translate3d(0,-50%,0)
      display: flex
      justify-content: center
      align-items: center
      padding: 0 20px 0 12px
      opacity: 0
      height: 100%
      @include breakpoint($tablet-max-1040)
        padding: 0 16px 0 8px
      &:before
        content: ''
        @include icon-close($color-black-secondary)
        @include breakpoint($tablet-max-1040)
          width: 10px
          min-width: 10px
          height: 10px
          min-height: 10px
      &.search__btn:hover
        opacity: 0
      &.search__btn--active:hover
        opacity: 0.75
    &--profile
      @include icon-user-circle
    &--back
      position: relative
      border-radius: 36px
      background: $color-gray-tertiary
      width: 52px
      height: 36px
      cursor: pointer
      transition: background 0.24s ease-in-out
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        @include icon-arrow-black-left
        position: absolute
        @include pos-all-center
      //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
    //@include breakpoint($tablet-max-1040)
    &--active
      opacity: 1
      //@include breakpoint($tablet-max-1040)
  &__input-text
    appearance: none
    user-select: none
    background: $color-gray-tertiary
    border-radius: 100px
    padding: 19.75px 44px
    font-family: $font-rubik
    font-style: normal
    font-weight: 500
    font-size: 14px
    line-height: 1
    letter-spacing: -0.1px
    color: $color-black-secondary
    border: none
    text-align: left
    text-transform: none
    width: 100%
    outline: none
    margin: 0
    display: block
    transition: color 0.3s ease, background 0.3s ease, opacity 0.3s ease
    @include breakpoint($tablet-max-1040)
      border-radius: 12px
      font-size: 15px
      padding: 13px 34px 13px 32px
    @include placeholder
      color: $color-gray-secondary
      transition: color 0.3s ease, background 0.3s ease
      //@include breakpoint($tablet-max-1040)
    &:hover
      border-color: $color-gray-primary
      //@include breakpoint($tablet-max-1040)
    &:focus
      background: rgba($color-gray-senary,0.29)
      //@include breakpoint($tablet-max-1040)
