.ui-kit
  padding: 64px 64px
  margin: 0 auto
  width: 100%
  max-width: 1220px
  @include breakpoint($tablet-max-1040)
    padding: 36px 24px
  &__item
    margin: 0 0 56px
    @include breakpoint($tablet-max-1040)
      margin-bottom: 36px
    //&--bg-white
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__item-title
    font-weight: 600
    font-size: 24px
    line-height: 1.24
    color: $color-black-primary
    margin: 0 0 16px
    letter-spacing: -0.03px
    @include breakpoint($tablet-max-1040)
      font-size: 18px
      letter-spacing: -0.02px
  &__item-content
    border: 1px solid rgba($color-black-secondary,0.7)
    border-radius: 20px
    padding: 50px 50px 50px 50px
    @include breakpoint($tablet-max-1040)
      padding: 14px 24px
    .ui-kit__item--bg-white &
      background: rgba($color-white-primary,0.9)
      //@include breakpoint($tablet-max-1040)
    .ui-kit__item--bg-orange &
      background: rgba($color-orange-primary,0.5)
      //@include breakpoint($tablet-max-1040)
    .button
      margin-bottom: 12px
      //@include breakpoint($tablet-max-1040)
      &:last-child
        margin-bottom: 0
        //@include breakpoint($tablet-max-1040)
