.content
  padding: 24px
  background: $color-white-primary
  border-radius: 32px 32px 32px 32px
  @include breakpoint($tablet-max-1040)
    padding: 12px 12px 12px 12px
    border-radius: 24px
  &__row
    margin: 0 0 40px
    @include breakpoint($tablet-max-1040)
      margin: 0 0 20px
    &--header
      margin: 0 0 32px
      @include breakpoint($tablet-max-1040)
        margin: 0 0 18px
      &:last-child
        margin-bottom: 0
        //@include breakpoint($tablet-max-1040)
      &.sticky
        padding: 12px 0 12px 0
        background: $color-white-primary
        border-radius: 0 0 0 0
        z-index: 9997
    &--align-row
      display: flex
      justify-content: flex-start
      align-items: center
      column-gap: 24px
      //@include breakpoint($tablet-max-1040)
    &--tags
      display: flex
      justify-content: flex-start
      align-items: center
      flex-wrap: wrap
      column-gap: 8px
      row-gap: 8px
      //@include breakpoint($tablet-max-1040)
    //&--tile-mosaic
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__title
    @include heading-1
    color: $color-gray-quaternary
    //@include breakpoint($tablet-max-1040)
  &__btn
    position: absolute
    z-index: 2
    //@include breakpoint($tablet-max-1040)
    &--back
      position: relative
      border-radius: 36px
      background: $color-gray-tertiary
      outline: none
      border: none
      width: 52px
      height: 36px
      cursor: pointer
      transition: background 0.24s ease-in-out
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        @include icon-arrow-black-left
        position: absolute
        @include pos-all-center
        //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
        //@include breakpoint($tablet-max-1040)
