.sign-in
  width: 100%
  height: 100%
  //@include breakpoint($tablet-max-1040)
  &__container
    width: 100%
    height: 100%
    //@include breakpoint($tablet-max-1040)
  &__step
    width: 100%
    height: 100%
    display: none
    flex-direction: column
    justify-content: center
    align-items: flex-start
    //@include breakpoint($tablet-max-1040)
    &--active
      display: flex
      //@include breakpoint($tablet-max-1040)
  &__content
    width: 100%
    display: flex
    justify-content: center
    align-items: center
    margin: auto 0 auto
    //@include breakpoint($tablet-max-1040)
    &--column
      flex-direction: column
      row-gap: 32px
      //@include breakpoint($tablet-max-1040)
    .button
      width: auto
      min-width: 295px
      //@include breakpoint($tablet-max-1040)
  &__content-row
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: center
    //@include breakpoint($tablet-max-1040)
  &__title
    @include login-title
    color: $color-gray-secondary
    margin: 0 0 6px
    //@include breakpoint($tablet-max-1040)
  &__input
    @include secondary-text-input
    //@include breakpoint($tablet-max-1040)
  &__footer
    display: flex
    justify-content: flex-start
    align-items: flex-start
    flex-direction: column
    row-gap: 12px
    //@include breakpoint($tablet-max-1040)
  //&__footer-row
  &__checkbox
    display: flex
    justify-content: flex-start
    align-items: center
    min-height: 20px
    position: relative
    padding-left: 30px
    cursor: pointer
    user-select: none
    //@include breakpoint($tablet-max-1040)
    input
      width: 0
      height: 0
      visibility: hidden
      opacity: 0
      position: absolute
      left: -9999px
      //@include breakpoint($tablet-max-1040)
      &:checked ~ .sign-in__checkbox-checkmark
        background-color: $color-red-primary
        //@include breakpoint($tablet-max-1040)
        &:after
          opacity: 1
          //@include breakpoint($tablet-max-1040)
    &:hover input
      ~ .sign-in__checkbox-checkmark
        background-color: $color-gray-tertiary
        //@include breakpoint($tablet-max-1040)
      &:checked ~ .sign-in__checkbox-checkmark
        background-color: $color-red-primary
        //@include breakpoint($tablet-max-1040)
      ~ .sign-in__checkbox__body
        color: $color-red-primary
        //@include breakpoint($tablet-max-1040)
  &__checkbox-checkmark
    position: absolute
    top: 1px
    left: 0
    height: 20px
    width: 20px
    background-color: $color-gray-tertiary
    transition: background-color 0.24s ease
    border-radius: 11px
    //@include breakpoint($tablet-max-1040)
    &:after
      content: ""
      position: absolute
      left: calc(50% + 0.3px)
      top: calc(50% + 0.23px)
      transform: translate3d(-50%, -50%, 0)
      @include icon-checkbox
      opacity: 0
      transition: opacity 0.24s ease
      //@include breakpoint($tablet-max-1040)
  &__checkbox-body
    @include login-text
    color: $color-gray-secondary
    transition: color 0.24s ease
    //@include breakpoint($tablet-max-1040)
    a
      @include login-link
      color: $color-gray-primary
      @include hover
      //@include breakpoint($tablet-max-1040)
  &__text
    font: 500 14px/1.28 $font-rubik
    letter-spacing: -0.38px
    color: $color-gray-secondary
    //@include breakpoint($tablet-max-1040)
    a
      @include login-link
      color: $color-gray-primary
      @include hover
      //@include breakpoint($tablet-max-1040)
  &__message
    @include login-error
    color: $color-red-primary
    border: none
    background: none
    user-select: none
    outline: none
    //@include breakpoint($tablet-max-1040)
    &--gray
      @include login-info
      color: $color-gray-secondary
      //@include breakpoint($tablet-max-1040)
    .sign-in--success &
      display: none
      //@include breakpoint($tablet-max-1040)
  &__input-wrapper
    display: grid
    grid-template-columns: repeat(4, 48px)
    grid-column-gap: 12px
    margin: 0 0 16px
    //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 16px
      //@include breakpoint($tablet-max-1040)
  &__input-num-code
    border: none
    outline: none
    border-radius: 12px
    height: 72px
    background: rgba($color-gray-secondary,0.2)
    color: $color-black-secondary
    font-family: $font-rubik
    font-size: 40px
    font-style: normal
    font-weight: 500
    line-height: 1
    letter-spacing: -0.38px
    padding: 16px 12px
    text-align: center
    transition: background 0.24s ease, color 0.24s ease
    //@include breakpoint($tablet-max-1040)
    @include placeholder
      color: $color-black-secondary
      //@include breakpoint($tablet-max-1040)
    .sign-in--error &
      background: rgba($color-red-primary,0.2)
      color: $color-red-primary
      //@include breakpoint($tablet-max-1040)
  &__preloader
    position: relative
    width: 26.666666666666667px
    height: 26.666666666666667px
    margin: auto
    transform: scale(0.6)
    display: none
    //@include breakpoint($tablet-max-1040)
    .sign-in--success &
      display: block
      //@include breakpoint($tablet-max-1040)
  &__preloader-item
    position: absolute
    background-color: $color-white-primary
    will-change: background-color
    height: calc(14px / 3)
    width: calc(14px / 3)
    border-radius: calc(7px / 3)
    animation-name: animatePreloader
    animation-duration: 0.672s
    animation-iteration-count: infinite
    animation-direction: normal
    //@include breakpoint($tablet-max-1040)
    &--first
      left: 0
      top: calc(32px / 3)
      animation-delay: 0.2495s
      //@include breakpoint($tablet-max-1040)
    &--second
      left: calc(9px / 3)
      top: calc(9px / 3)
      animation-delay: 0.336s
      //@include breakpoint($tablet-max-1040)
    &--third
      left: calc(32px / 3)
      top: 0
      animation-delay: 0.4225s
      //@include breakpoint($tablet-max-1040)
    &--fourth
      right: calc(9px / 3)
      top: calc(9px / 3)
      animation-delay: 0.509s
      //@include breakpoint($tablet-max-1040)
    &--fifth
      right: 0
      top: calc(32px / 3)
      animation-delay: 0.5955s
      //@include breakpoint($tablet-max-1040)
    &--sixth
      right: calc(9px / 3)
      bottom: calc(9px / 3)
      animation-delay: 0.672s
      //@include breakpoint($tablet-max-1040)
    &--seventh
      left: calc(32px / 3)
      bottom: 0
      animation-delay: 0.7585s
      //@include breakpoint($tablet-max-1040)
    &--eighth
      left: calc(9px / 3)
      bottom: calc(9px / 3)
      animation-delay: 0.845s
      //@include breakpoint($tablet-max-1040)
  @keyframes animatePreloader
    0%
      background-color: $color-red-primary
      //@include breakpoint($tablet-max-1040)
    100%
      background-color: $color-white-primary
      //@include breakpoint($tablet-max-1040)
