.basket
  border-radius: 32px
  overflow: hidden
  display: block
  width: 100%
  height: 100%
  position: relative
  .popup--basket &
    border-radius: 0
  &__container
    padding: 24px
    border-radius: 32px
    background: $color-white-primary
    display: flex
    flex-direction: column
    justify-content: space-between
    align-items: center
    position: relative
    width: 100%
    height: 100%
    overflow-y: scroll
    scrollbar-width: thin
    scrollbar-color: transparent transparent
    scrollbar-width: none
    //@include breakpoint($tablet-max-1040)
    &::-webkit-scrollbar
      display: none
      //@include breakpoint($tablet-max-1040)
    &--align-center
      justify-content: center
      //@include breakpoint($tablet-max-1040)
    .popup--basket &
      @include breakpoint($tablet-max-1040)
        padding: 0
        border-radius: 0
        row-gap: 12px
        //height: calc(100% - 26px)
        height: 100%
  &__row
    width: 100%
    //@include breakpoint($tablet-max-1040)
    &--top
      margin: 0 0 0
      @include breakpoint($tablet-max-1040)
        margin: 0 12px
      .basket__container--align-center &
        margin: 0 0 auto
        //@include breakpoint($tablet-max-1040)
      &:last-child
        margin-bottom: 0
        //@include breakpoint($tablet-max-1040)
    &--middle
      .basket__container--align-center &
        margin: auto 0
      //@include breakpoint($tablet-max-1040)
    &--bottom
      margin: auto 0 0 0
      padding-top: 12px
    //@include breakpoint($tablet-max-1040)
    &--mobile-to-bottom
      width: calc(100% - 24px)
      position: sticky
      left: 12px
      right: 0
      bottom: 12px
      background: $color-white-primary
      z-index: 1
  &__address
    padding: 0 44px 0 0
    text-decoration: none
    @include breakpoint($tablet-max-1040)
      //width: 85%
      //width: 100%
      //background: $color-white-primary
      //padding-bottom: 6px
    &--arrow
      //display: flex
      //justify-content: flex-start
      //align-items: flex-start
      position: relative
      text-decoration: none
      width: 100%
      padding: 0 20px 0 0
      margin: 0 0 12px
      min-height: 24px
      cursor: pointer
      @include breakpoint($tablet-max-1040)
        padding: 0 64px 0 0
      &:after
        content: ''
        @include icon-arrow-top-simple
        transform-origin: center center
        transform: rotate(90deg) translate3d(-50%, 0, 0)
        position: absolute
        right: 0
        top: 50%
        @include breakpoint($tablet-max-1040)
          top: 8px
          transform: translate3d(0,0,0) rotate(90deg)
          right: 48px
        @include breakpoint($mobile-max-640)
          width: calc(14px / 1.3)
          min-width: calc(14px / 1.3)
          height: calc(8px / 1.3)
          min-height: calc(8px / 1.3)
  &__address-title
    @include cart-address
    color: $color-gray-quaternary
    @include breakpoint($tablet-max-1040)
      padding: 0 0 0 0
    &--indent-right
      padding-right: 46px
      //@include breakpoint($tablet-max-1040)
  &__address-time
    @include cart-address
    color: $color-gray-secondary
    letter-spacing: -0.1px
    //@include breakpoint($tablet-max-1040)
  &__info
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    row-gap: 12px
    //@include breakpoint($tablet-max-1040)
  &__info-text
    font: 500 18px/1.22 $font-rubik
    color: $color-gray-secondary
    letter-spacing: -0.1px
    text-align: center
    //@include breakpoint($tablet-max-1040)
  //.button
  &__sum
    display: flex
    justify-content: flex-start
    align-items: center
    flex-direction: column
    row-gap: 6px
    margin: 0 0 16px
    //@include breakpoint($tablet-max-1040)
    .basket__offer-container--summary-info &
      margin: 0 0 42px
      //@include breakpoint($tablet-max-1040)
  &__sum-total
    @include cart-sum-title
    color: $color-gray-secondary
    //@include breakpoint($tablet-max-1040)
  &__sum-price
    @include cart-sum-price
    color: $color-black-secondary
    //@include breakpoint($tablet-max-1040)
  // offer
  &__offer
    width: 100%
    height: 100%
    display: flex
    flex-direction: column
    justify-content: space-between
    align-items: flex-start
    //@include breakpoint($tablet-max-1040)
  &__offer-container
    width: 100%
    padding-bottom: 36px
    .popup__row--status &
      padding-bottom: 0
    //@include breakpoint($tablet-max-1040)
    //  padding-bottom: 36px
      //padding-bottom: 10px
    &--space-around
      //padding: 48px 12px 32px
      padding: 48px 12px 12px
      @include breakpoint($tablet-max-1040)
        padding: 36px 0 0
    //&:last-child
    //  margin-bottom: 0
    &--summary-info
      background: $color-gray-tertiary
      padding: 24px 24px 24px
      width: calc(100% + 48px)
      transform: translate3d(-24px,24px,0)
      position: relative
      //@include breakpoint($tablet-max-1040)
      //&:after
      //  content: ''
      //  display: block
      //  width: 100%
      //  height: 100%
      //  position: absolute
      //  left: 0
      //  right: 0
      //  bottom: 0
      //  transform: translate3d(0,100%,0)
      //  background: $color-gray-tertiary
      //  @include breakpoint($tablet-max-1040)
      //    height: 24px
      //    transform: translate3d(0,24px,0)
    &--receipt
      position: sticky
      left: 0
      right: 0
      bottom: 0
      background: $color-white-primary
      z-index: 1
      padding-top: 12px
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        display: block
        background: $color-white-primary
        width: 100%
        height: 24px
        position: absolute
        left: 0
        right: 0
        bottom: -24px
        @include breakpoint($tablet-max-1040)
          height: 12px
          bottom: -12px
    &--gradient-top-to-bottom
      &:before
        content: ''
        display: none
        width: 100%
        height: 36px
        background: linear-gradient(180deg, rgba(255,255,255,1) 37%, rgba(255,255,255,0.020045518207282953) 100%)
        position: absolute
        left: 0
        right: 0
        bottom: 0
        transform: translate3d(0, calc(100% - 1px), 0)
      &.basket__offer-container--gradient-visible
        &:before
          @include breakpoint($tablet-max-1040)
            display: block
    &--gradient-bottom-to-top
      &:before
        content: ''
        display: none
        width: 100%
        height: 36px
        background: linear-gradient(0deg, rgba(255,255,255,1) 37%, rgba(255,255,255,0.020045518207282953) 100%)
        position: absolute
        left: 0
        right: 0
        top: 0
        transform: translate3d(0, calc(-100% + 1px), 0)
      &.basket__offer-container--gradient-visible
        &:before
          @include breakpoint($tablet-max-1040)
            display: block
  &__offer-row
    display: flex
    justify-content: flex-start
    align-items: center
    position: relative
    text-decoration: none
    width: 100%
    padding: 0 20px 0 0
    margin: 0 0 12px
    min-height: 24px
    cursor: pointer
    @include breakpoint($tablet-max-1040)
      padding: 0 22px 0 0
    &:after
      content: ''
      @include icon-arrow-top-simple
      transform-origin: center center
      transform: rotate(90deg) translate3d(-50%, 0, 0)
      position: absolute
      right: 0
      top: calc(50% + 2px)
      @include breakpoint($tablet-max-1040)
        top: calc(50% + 2px)
      @include breakpoint($mobile-max-640)
        width: calc(14px / 1.3)
        min-width: calc(14px / 1.3)
        height: calc(8px / 1.3)
        min-height: calc(8px / 1.3)
    &--without-arrow
      padding-right: 0
      //@include breakpoint($tablet-max-1040)
      &:after
        display: none
        //@include breakpoint($tablet-max-1040)
    &--remove
      &:after
        @include icon-trash
        opacity: 0.45
        transform: rotate(0) translate3d(0, -50%, 0)
        @include breakpoint($tablet-max-1040)
          transform: rotate(0) translate3d(0, 0, 0)
          top: 12px
    &--space-around
      padding-top: 12px
      padding-bottom: 12px
      @include breakpoint($tablet-max-1040)
        padding-top: 10px
        padding-bottom: 10px
    &--space-beetwen
      justify-content: space-between
      //@include breakpoint($tablet-max-1040)
    &--basket-add-address
      margin: 0
      padding-top: 12px
      padding-bottom: 12px
      //@include breakpoint($tablet-max-1040)
    //&--text-small
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__offer-col
    display: flex
    justify-content: flex-start
    align-items: flex-start
    flex-direction: column
    //@include breakpoint($tablet-max-1040)
    //&:first-child
    //&:last-child
  &__offer-title
    font: 500 18px/1.22 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-quaternary
    //@include breakpoint($tablet-max-1040)
  &__offer-descr
    font: 500 18px/1.22 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-secondary
    @include breakpoint($tablet-max-1040)
      font-size: 16px
  &__offer-img-link
    display: flex
    width: 80px
    height: 80px
    border-radius: 10px
    overflow: hidden
    position: relative
    margin: 0 10px 0 0
    @include breakpoint($tablet-max-1040)
      width: 60px
      height: 60px
      border-radius: 8px
      overflow: hidden
      position: relative
      margin: 0 6px 0 0
    &:last-child
      margin-right: 0
      //@include breakpoint($tablet-max-1040)
  &__offer-img
    display: block
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    object-fit: cover
    //@include breakpoint($tablet-max-1040)
  &__offer-subtitle
    font: 500 16px/1.5 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-quaternary
    //@include breakpoint($tablet-max-1040)
    .basket__offer-container--receipt &
      font: 500 16px/1.5 $font-rubik
      color: $color-gray-primary
      letter-spacing: -0.05px
      //@include breakpoint($tablet-max-1040)
    .basket__offer-row--text-small &
      font: 500 14px/1.28 $font-rubik
      letter-spacing: -0.05px
      color: $color-gray-primary
      //@include breakpoint($tablet-max-1040)
  &__offer-subdescr
    font: 500 14px/1.71 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-secondary
    //@include breakpoint($tablet-max-1040)
    .basket__offer-container--receipt &
      font: 500 18px/1.33 $font-rubik
      color: $color-gray-secondary
      letter-spacing: -0.38px
      //@include breakpoint($tablet-max-1040)
    .basket__offer-row--text-small &
      font: 500 14px/1.28 $font-rubik
      letter-spacing: -0.38px
      color: $color-gray-secondary
      //@include breakpoint($tablet-max-1040)
    &--black
      color: $color-gray-quaternary !important
      //@include breakpoint($tablet-max-1040)
  &__error-message
    @include login-error
    color: $color-red-primary
    border: none
    background: none
    user-select: none
    outline: none
    text-align: center
    margin: 12px 0 0 0
    @include breakpoint($tablet-max-1040)
      padding: 0 0 12px 0
    a
      color: $color-black-secondary
      //@include breakpoint($tablet-max-1040)
  &__order-status
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: center
    row-gap: 6px
    margin: 0 0 24px
    width: 100%
    //@include breakpoint($tablet-max-1040)
  &__order-status-icon
    width: 36px
    height: 36px
    border-radius: 18px
    display: flex
    justify-content: center
    align-items: center
    //@include breakpoint($tablet-max-1040)
    &:after
      content: ''
      //@include breakpoint($tablet-max-1040)
    .basket__order-status--fulfilled &
      background: $color-green-primary
      //@include breakpoint($tablet-max-1040)
      &:after
        @include icon-checkbox
        width: 16.74px
        min-width: 16.74px
        height: 12.925px
        min-height: 12.925px
        transform: translate3d(0.5px,0.5px,0)
        //@include breakpoint($tablet-max-1040)
    .basket__order-status--cancelled &
      background: $color-red-primary
      &:after
        @include icon-close($color-white-primary)
    .basket__order-status--pending-payment &
      background: $color-orange-primary
      //@include breakpoint($tablet-max-1040)
      &:after
        @include icon-attention($color-orange-primary)
        //@include breakpoint($tablet-max-1040)
    .basket__order-status--error &
      background: $color-red-primary
      //@include breakpoint($tablet-max-1040)
      &:after
        @include icon-close($color-white-primary)
        //@include breakpoint($tablet-max-1040)
    .basket__order-status--awaiting-confirmation &
      background: $color-gray-secondary
      //@include breakpoint($tablet-max-1040)
      &:after
        @include icon-attention($color-gray-secondary)
        //@include breakpoint($tablet-max-1040)
    .basket__order-status--awaiting-assembly &,
    .basket__order-status--awaiting-confirmation &
      background: $color-green-primary
      //@include breakpoint($tablet-max-1040)
      &:after
        @include icon-hourglass
        //@include breakpoint($tablet-max-1040)
    .basket__order-status--order-confirmed &
      background: $color-green-primary
      //@include breakpoint($tablet-max-1040)
      &:after
        @include icon-checkbox
        width: 16.74px
        min-width: 16.74px
        height: 12.925px
        min-height: 12.925px
        transform: translate3d(0.5px,0.5px,0)
        //@include breakpoint($tablet-max-1040)
  &__order-status-text
    @include cart-title-2
    color: $color-gray-quaternary
    //@include breakpoint($tablet-max-1040)
  &__btns-wrapper
    display: flex
    justify-content: center
    align-items: center
    column-gap: 12px
    margin: 0 0 48px
    width: 100%
    //@include breakpoint($tablet-max-1040)
    .button
      width: 188px
      @include breakpoint($tablet-max-1040)
        width: 50%
        padding-left: 0
        padding-right: 0
        font-size: 15px
  &__summary-info
    padding: 12px 0
    border-top: 1px solid $color-gray-quinary
    //@include breakpoint($tablet-max-1040)
    &:last-child
      border-bottom: 1px solid $color-gray-quinary
      .popup__row--status &
        padding-bottom: 24px
      //@include breakpoint($tablet-max-1040)
  &__summary-info-title
    font: 500 14px/1.28 $font-rubik
    letter-spacing: -0.6px
    color: $color-gray-secondary
    //@include breakpoint($tablet-max-1040)
  &__summary-info-descr
    font: 500 14px/1.28 $font-rubik
    letter-spacing: -0.6px
    color: $color-gray-primary
    //@include breakpoint($tablet-max-1040)
  // select address
  &__select-address
    height: 100%
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: flex-start
    @include breakpoint($tablet-max-1040)
      width: 100%
  &__select-address-container
    margin: 0 0 24px
    width: 100%
    //@include breakpoint($tablet-max-1040)
  &__select-address-map
    width: 100%
    height: 270px
    position: relative
    //@include breakpoint($tablet-max-1040)
  &__select-address-map-container
    position: absolute
    bottom:  0
    left: 0
    right: 0
    width: 100%
    height: 100%
    z-index: 2
    display: flex
    justify-content: center
    align-items: flex-end
    //@include breakpoint($tablet-max-1040)
    &:after
      content: ''
      display: block
      width: 100%
      height: 47%
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, #FFF 100%)
      //@include breakpoint($tablet-max-1040)
  &__select-address-map-header
    position: absolute
    width: 100%
    top: 0
    left: 0
    right: 0
    //@include breakpoint($tablet-max-1040)
    .button
      width: auto
      position: absolute
      right: 24px
      top: 24px
      //@include breakpoint($tablet-max-1040)
  &__select-address-map-btn
    position: absolute
    z-index: 2
    //@include breakpoint($tablet-max-1040)
    &--back
      position: relative
      border-radius: 36px
      background: $color-white-primary
      box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.08)
      width: 52px
      height: 36px
      left: 24px
      top: 24px
      cursor: pointer
      transition: background 0.24s ease-in-out
      @include breakpoint($tablet-max-1040)
        left: 12px
        top: 12px
      &:after
        content: ''
        @include icon-arrow-black-left
        position: absolute
        @include pos-all-center
        //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
        //@include breakpoint($tablet-max-1040)
    &--close
      @include icon-close-big($color-white-primary)
      right: 24px
      top: 24px
      cursor: pointer
      transition: background-image 0.24s ease-in-out
      @include breakpoint($tablet-max-1040)
        right: 12px
        top: 12px
      &:hover
        @include icon-close-big($color-gray-quinary)
        //@include breakpoint($tablet-max-1040)
    &--remove
      border-radius: 36px
      background: $color-white-primary
      //box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.08)
      width: 36px
      height: 36px
      right: 24px
      top: 24px
      cursor: pointer
      transition: background 0.24s ease-in-out
      @include breakpoint($tablet-max-1040)
        right: 12px
        top: 12px
      &:after
        content: ''
        @include icon-trash-2($color-black-primary)
        position: absolute
        @include pos-all-center
        //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
        //@include breakpoint($tablet-max-1040)
    &--edit
      border-radius: 36px
      background: $color-white-primary
      //box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.08)
      width: 36px
      height: 36px
      right: 24px
      top: 72px
      cursor: pointer
      transition: background 0.24s ease-in-out
      @include breakpoint($tablet-max-1040)
        top: 60px
        right: 12px
      &:after
        content: ''
        @include icon-edit
        position: absolute
        @include pos-all-center
        //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
        //@include breakpoint($tablet-max-1040)
  &__select-address-map-title
    font: 600 18px/2 $font-rubik
    letter-spacing: -0.38px
    color: $color-gray-quaternary
    position: absolute
    left: 50%
    transform: translate3d(-50%,0,0)
    top: 24px
    //@include breakpoint($tablet-max-1040)
  &__select-address-map-img
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    object-fit: cover
    width: 100%
    height: 100%
    z-index: 1
    //@include breakpoint($tablet-max-1040)
  &__select-address-title
    font: 600 20px/1.2 $font-rubik
    letter-spacing: -0.3px
    color: $color-gray-quaternary
    padding: 0 24px
    @include breakpoint($tablet-max-1040)
      font-size: 18px
  &__select-address-row
    padding: 0 24px
    width: 100%
    @include breakpoint($tablet-max-1040)
      padding-left: 12px
      padding-right: 12px
    &--button
      margin-top: auto
      margin-bottom: 0
      padding-top: 24px
      padding-bottom: 24px
      @include breakpoint($tablet-max-1040)
        padding-top: 12px
        padding-bottom: 12px
  // checkbpx
  &__payment
    width: 100%
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: flex-start
    row-gap: 12px
    //@include breakpoint($tablet-max-1040)
  &__checkbox
    display: flex
    justify-content: flex-start
    align-items: center
    align-content: center
    min-height: 20px
    position: relative
    padding-right: 30px
    cursor: pointer
    user-select: none
    width: 100%
    //@include breakpoint($tablet-max-1040)
    &--small
      padding-right: 0
      padding-left: 30px
      //@include breakpoint($tablet-max-1040)
    input
      width: 0
      height: 0
      visibility: hidden
      opacity: 0
      position: absolute
      left: -9999px
      //@include breakpoint($tablet-max-1040)
      &:checked ~ .basket__checkbox-checkmark
        background-color: $color-red-primary
        //@include breakpoint($tablet-max-1040)
        &:after
          opacity: 1
          //@include breakpoint($tablet-max-1040)
    &:hover input
      ~ .basket__checkbox-checkmark
        background-color: $color-gray-tertiary
        //@include breakpoint($tablet-max-1040)
      &:checked ~ .basket__checkbox-checkmark
        background-color: $color-red-primary
        //@include breakpoint($tablet-max-1040)
      ~ .basket__checkbox__body
        color: $color-red-primary
        //@include breakpoint($tablet-max-1040)
  &__checkbox-checkmark
    position: absolute
    top: 1px
    right: 0
    height: 20px
    width: 20px
    background-color: $color-gray-tertiary
    transition: background-color 0.24s ease
    border-radius: 11px
    //@include breakpoint($tablet-max-1040)
    .basket__checkbox--small &
      right: auto
      left: 0
      //@include breakpoint($tablet-max-1040)
    &:after
      content: ""
      position: absolute
      left: calc(50% + 0.3px)
      top: calc(50% + 0.23px)
      transform: translate3d(-50%, -50%, 0)
      @include icon-checkbox
      opacity: 0
      transition: opacity 0.24s ease
      //@include breakpoint($tablet-max-1040)
  &__checkbox-body
    font: 500 16px/1.5 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-quaternary
    transition: color 0.24s ease
    //@include breakpoint($tablet-max-1040)
    .basket__checkbox--small &
      @include login-text
      color: $color-gray-secondary
      transform: translate3d(0,0.8px,0)
      //@include breakpoint($tablet-max-1040)
    a
      @include login-link
      color: $color-gray-primary
      //@include breakpoint($tablet-max-1040)
      .basket__checkbox--small &
        @include login-link
        //@include breakpoint($tablet-max-1040)
  // get change
  &__input-simple-wrapper
    width: 100%
    display: flex
    justify-content: center
    align-items: center
