.map
  //@include breakpoint($tablet-max-1040)
  //&__container
  //&__header
  &__btn
    position: absolute
    z-index: 2
    //@include breakpoint($tablet-max-1040)
    &--back
      position: relative
      border-radius: 36px
      background: $color-white-primary
      box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.08)
      width: 52px
      height: 36px
      left: 12px
      top: 12px
      cursor: pointer
      transition: background 0.24s ease-in-out
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        @include icon-arrow-black-left
        position: absolute
        @include pos-all-center
        //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
        //@include breakpoint($tablet-max-1040)
    &--close
      @include icon-close-big($color-white-primary)
      right: 12px
      top: 12px
      cursor: pointer
      transition: background-image 0.24s ease-in-out
      //@include breakpoint($tablet-max-1040)
      &:hover
        @include icon-close-big($color-gray-quinary)
        //@include breakpoint($tablet-max-1040)
    &--plus
      width: 36px
      height: 36px
      border-radius: 36px
      background: var(--white-primary, #FFF)
      box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.08)
      position: relative
      cursor: pointer
      transition: background-image 0.24s ease-in-out
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        @include icon-plus($color-black-secondary)
        width: 20px
        min-width: 20px
        height: 20px
        min-height: 20px
        @include pos-all-center
        //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
        //@include breakpoint($tablet-max-1040)
    &--minus
      width: 36px
      height: 36px
      border-radius: 36px
      background: var(--white-primary, #FFF)
      box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.08)
      position: relative
      cursor: pointer
      transition: background-image 0.24s ease-in-out
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        @include icon-minus($color-black-secondary)
        width: 20px
        min-width: 20px
        height: 20px
        min-height: 20px
        @include pos-all-center
        //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
        //@include breakpoint($tablet-max-1040)
  &__controls
    position: absolute
    right: 12px
    top: 50%
    transform: translate3d(0,-50%,0)
    z-index: 2
    display: flex
    flex-direction: column
    justify-content: space-between
    align-items: center
    row-gap: 12px
    //@include breakpoint($tablet-max-1040)
  &__content
    position: absolute
    bottom:  0
    left: 0
    right: 0
    width: 100%
    height: 30.41871921182266%
    z-index: 2
    display: flex
    justify-content: flex-end
    align-items: center
    flex-direction: column
    row-gap: 24px
    //@include breakpoint($tablet-max-1040)
    &--bg-gradient
      border-radius: 0 0 24px 24px
      background: linear-gradient(180deg, rgba(217, 217, 217, 0.00) 0%, #FFF 55.73%)
      @include breakpoint($tablet-max-1040)
        padding: 0 0 24px
    .button
      display: inline-flex
      width: auto
      margin: 0 16px 0 0
      //@include breakpoint($tablet-max-1040)
      &:last-child
        margin-right: 0
        //@include breakpoint($tablet-max-1040)
  &__content-title
    font: 600 18px/1.222 $font-rubik
    letter-spacing: -0.2px
    color: $color-gray-primary
  &__content-btns
    display: flex
    justify-content: center
    align-items: flex-end
  &__img
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    object-fit: cover
    width: 100%
    height: 100%
    z-index: 1
    //@include breakpoint($tablet-max-1040)
