.faq
  //@include breakpoint($tablet-max-1040)
  //&__container
    //@include breakpoint($tablet-max-1040)
  &__item
    padding: 18px 0 18px
    border-bottom: 1px solid $color-gray-quinary
    cursor: pointer
    @include breakpoint($tablet-max-1040)
      padding: 12px 0 12px
  &__item-title
    font: 500 16px/1.12 $font-rubik
    color: $color-gray-quaternary
    letter-spacing: -0.38px
    position: relative
    @include breakpoint($tablet-max-1040)
      padding-right: 22px
    &:after
      content: ''
      @include icon-arrow-top-simple
      transform-origin: center center
      position: absolute
      right: 0
      top: 5.5px
      transform: rotate(180deg)
      transition: transform 0.2s ease
      //@include breakpoint($tablet-max-1040)
      .faq__item--active &
        transform: rotate(0)
        //@include breakpoint($tablet-max-1040)
  &__item-content
    font: 500 14px/1.28 $font-rubik
    color: $color-gray-primary
    letter-spacing: -0.38px
    margin: 12px 0 0
    display: none
    @include breakpoint($tablet-max-1040)
      margin: 8px 0 0
    @include breakpoint($mobile-max-480)
      font-weight: 400
    .faq__item--active &
      display: block
      //@include breakpoint($tablet-max-1040)
