.basket-product
  width: 100%
  padding: 0 0 12px
  margin: 0 0 12px
  border-bottom: 1px solid $color-gray-quinary
  //@include breakpoint($tablet-max-1040)
  &:first-child
    margin-top: 12px
    //@include breakpoint($tablet-max-1040)
  &:last-child
    padding-bottom: 0
    border-bottom: none
    //margin-bottom: 0
    //@include breakpoint($tablet-max-1040)
  //&--info
  &__container
    display: grid
    grid-template-columns: 80px minmax(0, 1fr) minmax(0, 1fr)
    column-gap: 6px
    width: 100%
    //@include breakpoint($tablet-max-1040)
    .basket-product--info &
      grid-template-columns: 80px minmax(0, 1fr)
      //@include breakpoint($tablet-max-1040)
  &__warning
    margin: 12px 0 0 0
    display: flex
    justify-content: space-between
    align-items: center
    column-gap: 56px
    //@include breakpoint($tablet-max-1040)
    .button
      width: auto
      display: flex
      //@include breakpoint($tablet-max-1040)
  &__warning-text
    width: 100%
    @include cart-product-error
    color: $color-red-primary
    //@include breakpoint($tablet-max-1040)
  &__col
    &--left
      grid-column: 1 / 2
      //@include breakpoint($tablet-max-1040)
    &--center
      grid-column: 2 / 3
      //@include breakpoint($tablet-max-1040)
      display: flex
      justify-content: space-between
      align-items: flex-start
      flex-direction: column
      //@include breakpoint($tablet-max-1040)
    &--right
      display: flex
      justify-content: space-between
      align-items: flex-end
      flex-direction: column
      //@include breakpoint($tablet-max-1040)
  &__link
    grid-column: 3 / 4
    display: flex
    width: 80px
    height: 80px
    border-radius: 10px
    overflow: hidden
    position: relative
    //@include breakpoint($tablet-max-1040)
  &__sold-out
    width: 100%
    height: 100%
    font: 500 12px/1.5 $font-rubik
    letter-spacing: -0.1px
    color: $color-white-primary
    border-radius: 10px
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    background: rgba($color-black-secondary, 0.40)
    z-index: 3
    display: flex
    justify-content: center
    align-items: center
    //@include breakpoint($tablet-max-1040)
  &__discount-label
    font: 500 8px/10px $font-rubik
    letter-spacing: -0.01px
    color: $color-white-primary
    display: flex
    min-height: 15px
    padding: 2px
    align-items: center
    border-radius: 24px
    background: $color-green-primary
    position: absolute
    left: 6px
    top: 6px
    z-index: 2
    //@include breakpoint($tablet-max-1040)
  &__img
    display: block
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    object-fit: cover
    z-index: 1
    //@include breakpoint($tablet-max-1040)
  &__title
    @include cart-product-title
    color: $color-gray-primary
    margin: 0 0 2px
    //@include breakpoint($tablet-max-1040)
  &__subtitle
    @include cart-quantity
    color: $color-gray-secondary
    margin: 0 0 auto
    //@include breakpoint($tablet-max-1040)
  &__controls
    display: flex
    justify-content: space-between
    align-items: center
    flex-direction: row
    column-gap: 12px
    //@include breakpoint($tablet-max-1040)
  //&__controls-col
  //  &--remove-product
  //  &--add-product
  //  &--active
  &__controls-ctrl
    transition: background 0.24s ease, opacity 0.24s ease
    cursor: pointer
    //@include breakpoint($tablet-max-1040)
    &--minus
      @include icon-minus-circle($color-gray-senary)
      //@include breakpoint($tablet-max-1040)
      .basket-product__controls-col--remove-product.basket-product__controls-col--active:hover &
        opacity: 0.8
        //@include breakpoint($tablet-max-1040)
      .basket-product__controls-col--active &
        @include icon-minus-circle($color-gray-primary)
        //@include breakpoint($tablet-max-1040)
      .basket-product--discount .basket-product__controls-col--active &
        @include icon-minus-circle($color-green-secondary)
        //@include breakpoint($tablet-max-1040)
    &--plus
      @include icon-plus-circle($color-gray-senary)
      //@include breakpoint($tablet-max-1040)
      .basket-product__controls-col--add-product.basket-product__controls-col--active:hover &
        opacity: 0.8
        //@include breakpoint($tablet-max-1040)
      .basket-product__controls-col--active &
        @include icon-plus-circle($color-gray-primary)
        //@include breakpoint($tablet-max-1040)
      .basket-product--discount &
        @include icon-plus-circle($color-green-secondary)
        //@include breakpoint($tablet-max-1040)
  &__controls-counter
    @include cart-counter
    color: $color-gray-primary
    //@include breakpoint($tablet-max-1040)
  &__btn
    &--remove
      width: 20px
      min-width: 20px
      height: 20px
      min-height: 20px
      position: relative
      display: block
      border: none
      outline: none
      background-color: transparent
      cursor: pointer
      //@include breakpoint($tablet-max-1040)
      &:after
        content: ''
        @include icon-trash-2($color-gray-secondary)
        border: none
        outline: none
        background-color: transparent
        position: absolute
        left: 50%
        top: 50%
        transform: translate3d(-50%, -50%, 0)
        transition: background-color 0.24s ease
        //@include breakpoint($tablet-max-1040)
      &:hover:after
        @include icon-trash-2($color-red-primary)
        //@include breakpoint($tablet-max-1040)
  &__price
    font: 600 14px/26px $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-primary
    min-height: 26px
    //@include breakpoint($tablet-max-1040)
    .basket-product--discount &
      display: flex
      justify-content: flex-start
      align-items: center
      align-content: center
      column-gap: 4px
      border-radius: 40px
      padding: 7px 12px
      background: $color-green-secondary
      //@include breakpoint($tablet-max-1040)
  &__price-item
    white-space: nowrap
    &--old
      @include cart-price-old
      line-height: 18px
      color: rgba($color-black-secondary, 0.45)
      //@include breakpoint($tablet-max-1040)
    &--new
      @include cart-price-new
      color: $color-white-primary
      //@include breakpoint($tablet-max-1040)
  &__quantity-and-price
    margin: 0 0 0 auto
    //@include breakpoint($tablet-max-1040)
    span
      &:first-child
        font: 600 18px/1.22 $font-rubik
        letter-spacing: -0.32px
        color: $color-gray-quaternary
        @include breakpoint($tablet-max-1040)
          font: 600 16px/1.18 $font-rubik
          letter-spacing: -0.05px
      &:last-child
        font: 600 16px/1.18 $font-rubik
        letter-spacing: -0.05px
        color: $color-gray-primary
        position: relative
        justify-content: flex-start
        align-items: center
        margin: 0 0 0 4px
        //@include breakpoint($tablet-max-1040)
        &:before
          content: '•'
          margin: 0 4px 0 0
          font: 600 18px/1.122 $font-rubik
          letter-spacing: -0.32px
          color: $color-gray-quaternary
          opacity: 0.5
          //@include breakpoint($tablet-max-1040)
