*,
*:before,
*:after
  margin: 0
  padding: 0
  box-sizing: border-box
html
  height: 100%
  position: relative
  margin: 0
  padding: 0
  scroll-behavior: smooth
  @include breakpoint($tablet-max-1040)
    height: -webkit-fill-available
body
  margin: 0
  padding: 0
  position: relative
  width: 100%
  min-height: 100%
  background-color: $color-gray-tertiary
  scroll-behavior: smooth
  overflow-x: hidden
  @include breakpoint($tablet-max-1040)
    overflow-x: hidden
    overscroll-behavior: none
    scrollbar-width: none
    &::-webkit-scrollbar
      display: none
  &.hidden-scroll
    overflow: hidden
    scrollbar-width: none
    &::-webkit-scrollbar
      display: none
