.confirm-address
  padding: 24px
  position: absolute
  left: 0
  right: 0
  top: 0
  border-radius: 0 0 12px 12px
  background: $color-white-primary
  z-index: 9999
  display: none
  //@include breakpoint($tablet-max-1040)
  &--active
    @include breakpoint($tablet-max-1040)
      display: block
  &__container
    display: flex
    justify-content: flex-start
    align-items: flex-start
    flex-direction: column
    row-gap: 12px
    //@include breakpoint($tablet-max-1040)
  &__row
    display: flex
    justify-content: flex-start
    align-items: center
    column-gap: 10px
    //@include breakpoint($tablet-max-1040)
  &__title
    font: 600 18px/1.22 $font-rubik
    letter-spacing: -0.2px
    color: $color-black-secondary
    //@include breakpoint($tablet-max-1040)
  &__descr
    font: 500 16px/1.18 $font-rubik
    color: $color-gray-secondary
    letter-spacing: -0.3px
    //@include breakpoint($tablet-max-1040)
  //.button
