.grid
  position: relative
  height: 100%
  will-change: height
  //@include breakpoint($tablet-max-1040)
  &__container
    @include glass(1664px)
    display: flex
    justify-content: center
    column-gap: 24px
    will-change: height
    min-height: 100vh
    @include breakpoint($tablet-max-1040)
      justify-content: flex-start
      flex-direction: column
      column-gap: 0
      padding-left: 0
      padding-right: 0
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__col
    position: relative
    will-change: height
    max-width: 100%
    //@include breakpoint($tablet-max-1040)
    &--left,
    &--right
      margin: 0
      padding: 0
      position: sticky
      top: 0
      height: 100%
      max-height: 100vh
      @include breakpoint($tablet-max-1040)
        position: relative
        display: none
    &--left
      width: 100%
      min-width: 236px
      max-width: 260px
      height: calc(100vh - 16px)
      @include breakpoint($tablet-max-1040)
        max-width: 100%
        height: auto
    &--right
      top: 30px
      min-width: 336px
      width: 336px
      height: calc(100vh - 46px)
      @include breakpoint($tablet-max-1040)
        display: none !important
        //width: 100%
        //height: auto
    &--center
      position: relative
      width: calc(100% - 620px)
      max-width: 1144px
      min-height: 100%
      top: 30px
      overflow: hidden
      @include breakpoint($tablet-max-1040)
        width: 100%
        top: auto
    &--include-rows
      display: flex
      flex-direction: column
      justify-content: flex-start
      align-items: flex-start
      row-gap: 16px
      //@include breakpoint($tablet-max-1040)
