// GRID : begin
// main parent container (glass, container)
@mixin glass($max-width)
  width: 100%
  max-width: $max-width
  margin-left: auto
  margin-right: auto
  padding-left: 24px
  padding-right: 24px
  position: relative
  //@include breakpoint($tablet-max-1040)
  //@include breakpoint($mobile-max-375)
// GRID : end
// ICONS
@mixin icon
  display: block
  background-position: center center
  background-repeat: no-repeat
  background-size: contain
// FORM ELEMENTS : begin
// placeholder
@mixin placeholder
  &::-webkit-input-placeholder
    @content
  &::-moz-placeholder
    @content
  &:-moz-placeholder
    @content
  &:-ms-input-placeholder
    @content
  &:focus::-webkit-input-placeholder
    color: transparent
  &:focus::-moz-placeholder
    color: transparent
  &:focus:-moz-placeholder
    color: transparent
  &:focus:-ms-input-placeholder
    color: transparent
// primary input text
@mixin primary-text-input
  appearance: none
  user-select: none
  background: transparent
  border-radius: 0
  padding: 0 0 3px 0
  font-family: $font-rubik
  font-style: normal
  font-weight: 600
  font-size: 20px
  line-height: 1
  letter-spacing: -0.3px
  color: $color-black-secondary
  border: none
  border-bottom: 1px solid $color-gray-quinary
  text-align: left
  text-transform: none
  width: 100%
  outline: none
  margin: 0
  display: block
  transition: color 0.3s ease, border-color 0.3s ease
  //@include breakpoint($tablet-max-1040)
  @include placeholder
    color: $color-gray-quinary
    transition: color 0.3s ease, border-color 0.3s ease
    //@include breakpoint($tablet-max-1040)
  &:hover
    border-color: $color-gray-primary
    //@include breakpoint($tablet-max-1040)
  &:focus
    border-color: $color-black-secondary
    color: $color-black-secondary
    //@include breakpoint($tablet-max-1040)
  &--error
    color: $color-red-primary
    //@include breakpoint($tablet-max-1040)
