// checkbox
@mixin checkbox-default
  position: relative
  display: block
  border: none
  outline: none
  user-select: none
  height: 0
  width: 0
  cursor: pointer
  //@include breakpoint($tablet-max-1040)
  &:before
    content: ''
    display: block
    position: absolute
    top: -14px
    left: -23px
    height: 28px
    width: 46px
    background-color: $color-gray-senary
    will-change: background-color
    border-radius: 14px
    transition: background-color .2s
    cursor: pointer
    //@include breakpoint($tablet-max-1040)
  &:after
    content: ''
    display: block
    position: absolute
    will-change: transform
    transition: transform .2s, -webkit-transform .2s
    top: -12px
    left: -21px
    border-radius: 12px
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3)
    background-color: #fff
    height: 24px
    width: 24px
    cursor: pointer
    //@include breakpoint($tablet-max-1040)
  &:checked:before
    background-color: $color-red-primary
    //@include breakpoint($tablet-max-1040)
  &:checked:after
    transform: translateX(18px)
    //@include breakpoint($tablet-max-1040)
