.rules
  width: 100%
  //@include breakpoint($tablet-max-1040)
  &__container
    display: flex
    justify-content: flex-start
    align-items: flex-start
    flex-direction: column
    width: 100%
    //@include breakpoint($tablet-max-1040)
  a
    font: 500 16px/1.12 $font-rubik
    color: $color-gray-quaternary
    letter-spacing: -0.38px
    position: relative
    padding: 18px 0 18px
    border-bottom: 1px solid $color-gray-quinary
    cursor: pointer
    text-decoration: none
    width: 100%
    //@include breakpoint($tablet-max-1040)
    &:after
      content: ''
      @include icon-arrow-top-simple
      transform-origin: center center
      position: absolute
      right: 0
      top: calc(50% - 4px)
      transform: rotate(90deg) translate3d(0,-50%,0)
      transition: transform 0.2s ease
      //@include breakpoint($tablet-max-1040)
