.header
  display: none
  padding: 24px 0
  @include breakpoint($tablet-max-1040)
    display: block
  &__container
    display: flex
    justify-content: space-between
    align-items: center
    column-gap: 32px
    width: 100%
    padding: 0 24px
    @include breakpoint($mobile-max-375)
      padding: 0 12px
  //&__col
  &__address
    font: 600 18px/1.222 $font-rubik
    letter-spacing: -.2px
    color: $color-gray-quaternary
    cursor: pointer
    display: flex
    justify-content: flex-start
    align-items: center
    column-gap: 6px
    &:after
      content: ''
      @include icon-arrow-top-simple
      transform-origin: center center
      transform: rotate(180deg)
  &__text
    font: 500 15px/1.2 $font-rubik
    letter-spacing: -0.1px
    color: $color-black-secondary
    display: flex
    justify-content: flex-start
    align-items: center
    @include breakpoint($mobile-max-480)
      font-size: 14px
    &--shop-opening-hours
      &:before
        content: ''
        @include icon-moon
        margin: 0 6px 0 0
