.helpline
  //@include breakpoint($tablet-max-1040)
  //&__container
    //@include breakpoint($tablet-max-1040)
  &__title
    font: 600 24px/1.16 $font-rubik
    color: $color-gray-primary
    letter-spacing: -0.4px
    margin: 0 0 16px
    //@include breakpoint($tablet-max-1040)
  &__text
    font: 500 16px/1.12 $font-rubik
    color: $color-gray-quaternary
    letter-spacing: -0.38px
    margin:  0 0 24px
    //@include breakpoint($tablet-max-1040)
    &--gray
      color: $color-gray-secondary
      //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
    a
      color: $color-red-primary
      text-decoration: underline
      @include hover
      //@include breakpoint($tablet-max-1040)
  .button
    margin: 0 0 40px
    //@include breakpoint($tablet-max-1040)
