.sidebar
  padding: 0
  display: block
  height: 100%
  position: relative
  //@include breakpoint($tablet-max-1040)
  &--rounded
    border-radius: 32px
    overflow: hidden
    //@include breakpoint($tablet-max-1040)
  &__container
    display: none
    flex-direction: column
    justify-content: space-between
    align-items: flex-start
    row-gap: 16px
    width: 100%
    height: 100%
    column-gap: 16px
    scrollbar-width: none
    position: relative
    //@include breakpoint($tablet-max-1040)
    &::-webkit-scrollbar
      display: none
      //@include breakpoint($tablet-max-1040)
    &--active,
    &--visible
      display: flex
      //@include breakpoint($tablet-max-1040)
  &__row
    background: $color-white-primary
    width: 100%
    position: relative
    //@include breakpoint($tablet-max-1040)
    &--logo
      display: flex
      justify-content: center
      align-items: center
      border-radius: 0 0 20px 20px
      padding: 4px 0
      //@include breakpoint($tablet-max-1040)
    &--nav
      padding: 0 0 0 16px
      border-radius: 20px 20px 20px 20px
      height: 100%
      overflow: hidden
      //@include breakpoint($tablet-max-1040)
  &__cover
    z-index: 2
    position: absolute
    left: 0
    right: 0
    bottom: 0
    //@include breakpoint($tablet-max-1040)
  &__title
    font: 700 24px/1.16 $font-rubik
    color: $color-white-primary
    letter-spacing: -0.4px
    padding: 0 20px
    margin: 0 0 32px
    //@include breakpoint($tablet-max-1040)
  &__btns-wrapper
    padding: 16px 20px 20px 20px
    border-radius: 32px
    background: $color-white-primary
    display: flex
    flex-direction: column
    align-items: flex-start
    justify-content: flex-start
    row-gap: 16px
    position: relative
    width: 100%
    //@include breakpoint($tablet-max-1040)
  &__bg
    object-fit: cover
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    //@include breakpoint($tablet-max-1040)
  &__map
    padding: 20px
    border-radius: 24px
    background: $color-white-primary
    width: 100%
    z-index: 2
    //@include breakpoint($tablet-max-1040)
  //&__confirm-address
  &__confirm-address-title
    font: 700 20px/1.18 $font-rubik
    color: $color-black-primary
    letter-spacing: -.3px
    margin: 0 0 12px
    //@include breakpoint($tablet-max-1040)
  &__confirm-address-text
    font: 500 16px/1.1875 $font-rubik
    letter-spacing: -0.3px
    color: $color-gray-secondary
    margin: 0 0 12px
    //@include breakpoint($tablet-max-1040)
  &__confirm-address-btns
    display: flex
    justify-content: flex-start
    align-content: center
    column-gap: 12px
    //@include breakpoint($tablet-max-1040)
