.button
  &--red
    @include btn-red
    //@include breakpoint($tablet-max-1040)
  &--gray
    @include btn-gray
    //@include breakpoint($tablet-max-1040)
  //&--gray-small
  //  @include btn-gray
  //  font-size: 16px
  //  font-weight: 600
  //  letter-spacing: -0.05px
  //  background: $color-gray-tertiary
  //  @include breakpoint($tablet-max-1040)
  &--white
    @include btn-white
    //@include breakpoint($tablet-max-1040)
  &--icon
    padding-top: 16px
    padding-bottom: 16px
    //@include breakpoint($tablet-max-1040)
  &--icon-before
    .icon
      margin: 0 12px 0 0
      //@include breakpoint($tablet-max-1040)
  &--icon-after
    .icon
      margin: 0 0 0 12px
      //@include breakpoint($tablet-max-1040)
  &--two-lines
    line-height: 1.062
    padding-top: 11.05px
    padding-bottom: 11.05px
    flex-direction: column
    justify-content: center
    align-items: center
    //@include breakpoint($tablet-max-1040)
  .icon--plus
    @include icon-plus($color-white-primary)
    //@include breakpoint($tablet-max-1040)
  &__text
    &--small
      font: 500 13px/1.15 $font-rubik
      //@include breakpoint($tablet-max-1040)
  &--small
    color: $color-gray-quaternary
    font-size: 14px
    font-weight: 500
    letter-spacing: -0.1px
    min-height: 36px
    padding: 11px 16px
    //@include breakpoint($tablet-max-1040)
    &.button--icon-before
      .icon
        margin-right: 6px
        //@include breakpoint($tablet-max-1040)
    &.button--icon-after
      .icon
        margin-left: 6px
        //@include breakpoint($tablet-max-1040)
    &.button--icon-before,
    &.button--icon-after
      padding-top: 10px
      padding-bottom: 10px
      //@include breakpoint($tablet-max-1040)
    &.button--red
      color: $color-white-primary
      //@include breakpoint($tablet-max-1040)
  &--preloader
    display: flex
    justify-content: center
    align-items: center
    padding: 15px 30px
    //@include breakpoint($tablet-max-1040)
  &__preloader
    position: relative
    width: 26.666666666666667px
    height: 26.666666666666667px
    margin: auto
    transform: scale(0.6)
    display: block
    //@include breakpoint($tablet-max-1040)
  &__preloader-item
    position: absolute
    background-color: $color-white-primary
    will-change: background-color
    height: calc(14px / 3)
    width: calc(14px / 3)
    border-radius: calc(7px / 3)
    animation-name: animatePreloader
    animation-duration: 0.672s
    animation-iteration-count: infinite
    animation-direction: normal
    //@include breakpoint($tablet-max-1040)
    &--first
      left: 0
      top: calc(32px / 3)
      animation-delay: 0.2495s
      //@include breakpoint($tablet-max-1040)
    &--second
      left: calc(9px / 3)
      top: calc(9px / 3)
      animation-delay: 0.336s
      //@include breakpoint($tablet-max-1040)
    &--third
      left: calc(32px / 3)
      top: 0
      animation-delay: 0.4225s
      //@include breakpoint($tablet-max-1040)
    &--fourth
      right: calc(9px / 3)
      top: calc(9px / 3)
      animation-delay: 0.509s
      //@include breakpoint($tablet-max-1040)
    &--fifth
      right: 0
      top: calc(32px / 3)
      animation-delay: 0.5955s
      //@include breakpoint($tablet-max-1040)
    &--sixth
      right: calc(9px / 3)
      bottom: calc(9px / 3)
      animation-delay: 0.672s
      //@include breakpoint($tablet-max-1040)
    &--seventh
      left: calc(32px / 3)
      bottom: 0
      animation-delay: 0.7585s
      //@include breakpoint($tablet-max-1040)
    &--eighth
      left: calc(9px / 3)
      bottom: calc(9px / 3)
      animation-delay: 0.845s
      //@include breakpoint($tablet-max-1040)
  @keyframes animatePreloader
    0%
      background-color: $color-red-primary
      //@include breakpoint($tablet-max-1040)
    100%
      background-color: $color-white-primary
      //@include breakpoint($tablet-max-1040)
