.popup
  position: fixed
  left: 0
  right: 0
  top: 0
  bottom: 0
  z-index: 9999
  width: 100%
  height: 100vh
  will-change: opacity, visibility
  visibility: hidden
  opacity: 0
  @include breakpoint($tablet-max-1040)
    left: 0
    right: 0
    bottom: 0
    top: auto
    height: 100%
    height: -webkit-fill-available
  //&--big
  //&--gallery
  &--active
    visibility: visible
    opacity: 1
    transition: opacity 0.1s ease
    //@include breakpoint($tablet-max-1040)
  &--mobile-search
    display: none
    @include breakpoint($tablet-max-1040)
      display: block
  //&--basket
  &__container
    z-index: 9998
    background: $color-white-primary
    border-radius: 32px
    width: 100%
    min-width: 420px
    max-width: 484px
    position: absolute
    top: 50%
    will-change: transform
    transform: translate3d(calc(100% + 32px), -50%, 0)
    transition: transform 0.5s ease
    right: 32px
    height: calc(100vh - 64px)
    padding: 0 24px 24px
    overflow-y: scroll
    scrollbar-width: none
    &::-webkit-scrollbar
      display: none
    @include breakpoint($tablet-max-1040)
      min-width: 100%
      max-width: 100%
      left: 0
      right: 0
      bottom: 0
      top: 0
      border-radius: 0
      transform: translate3d(0,0,0) !important
      padding: 12px
      height: 100%
      overflow-x: hidden
      overscroll-behavior: none
      min-height: -webkit-fill-available
      &::-webkit-scrollbar
        display: none
      //@include breakpoint($tablet-max-1040)
    .popup--big &
      max-width: 978px
      padding: 0
      border: 12px solid $color-white-primary
      @include breakpoint($tablet-max-1040)
        width: 100%
        max-width: 100%
        padding: 0
        height: 100vh
        border: none
        border-radius: 0
        transform: translate3d(0, 0, 0) !important
        left: 0
        right: 0
        top: 0
        bottom: 0
    .popup--border-large &
      border: 24px solid $color-white-primary
      //@include breakpoint($tablet-max-1040)
    .popup--add-address &
      &:after
        content: ''
        display: block
        width: 100%
        height: 80px
        background: linear-gradient(0deg, rgba(255,255,255,1) 24%, rgba(255,255,255,0.6418942577030813) 100%)
        position: sticky
        left: 0
        right: 0
        bottom: -24px
        //@include breakpoint($tablet-max-1040)
    .popup--active &
      transform: translate3d(0, -50%, 0)
      //@include breakpoint($tablet-max-1040)
    .popup--remove-internal-indents &
      padding: 0 0 0 0
      //@include breakpoint($tablet-max-1040)
    .popup--align-column &
      display: flex
      justify-content: flex-start
      align-items: flex-start
      flex-direction: column
      //@include breakpoint($tablet-max-1040)
      .button
        margin-top: auto
        //@include breakpoint($tablet-max-1040)
    .popup--mobile-search &
      background: $color-gray-tertiary
    .popup--basket &,
    .popup--product-card &,
    .popup--profile &,
    .popup--sign-in &,
    .popup--add-address &
      @include breakpoint($tablet-max-1040)
        //height: calc(100% - 55px)
        //min-height: calc(100% - 55px)
        //bottom: 0
        //top: auto
        display: flex
        justify-content: flex-start
        align-items: flex-start
        flex-direction: column
        row-gap: 0
    .popup--sign-in &
      @include breakpoint($tablet-max-1040)
        justify-content: space-between
    .popup--basket &,
    .popup--product-card &,
    .popup--profile &,
    .popup--sign-in &,
    .popup--add-address &
      @include breakpoint($tablet-max-1040)
        transform: translate3d(0,100%,0) !important
        will-change: transform
        //border-radius: 0 0 0 0
        transition: transform 0.25s ease-out
    .popup--product-card &
      @include breakpoint($tablet-max-1040)
        border: none
    .popup--basket.popup--active &,
    .popup--product-card.popup--active &,
    .popup--profile &,
    .popup--sign-in.popup--active &,
    .popup--add-address.popup--active &
      @include breakpoint($tablet-max-1040)
        transform: translate3d(0,0,0) !important
        border-radius: 24px 24px 0 0
  &__row
    display: flex
    justify-content: flex-start
    align-items: flex-start
    flex-direction: column
    width: 100%
    //@include breakpoint($tablet-max-1040)
    &--title
      padding: 24px 0
      display: flex
      justify-content: space-between
      align-items: flex-start
      flex-direction: row
      position: sticky
      top: 0
      background: $color-white-primary
      //min-height: 82px
      z-index: 3
      @include breakpoint($tablet-max-1040)
        padding: 0
        //min-height: 58px
      .popup--mobile-search &
        @include breakpoint($tablet-max-1040)
          width: calc(100% + 24px)
          transform: translate3d(-12px,-12px,0)
          padding: 12px 12px
          //min-height: 60px
          background: $color-white-primary
      .popup--add-address &,
      .popup--orders &
        @include breakpoint($tablet-max-1040)
          padding: 0 0 12px
          transform: translate3d(0,-12px,0)
          border-top: 12px solid $color-white-primary
      .popup--addresses &,
      .popup--addresses-delete &,
      .popup--settings &,
      .popup--customise-notifications &,
      .popup--default &
        @include breakpoint($tablet-max-1040)
          padding: 0 0 12px
          transform: translate3d(0, -12px, 0)
          border-top: 12px solid #FFFFFF
      .popup--title-big &
        @include breakpoint($tablet-max-1040)
          padding-bottom: 12px !important
      //.popup--addresses &,
      //.popup--addresses-delete &,
      //.popup--settings &
      .popup--profile &
        @include breakpoint($tablet-max-1040)
          //min-height: 36px
      .popup--orders &
        @include breakpoint($tablet-max-1040)
          //min-height: 70px !important
      .popup--basket &
        @include breakpoint($tablet-max-1040)
          //min-height: 58px !important
      .popup--basket-empty &
        min-height: 48px !important
      .button
        width: auto
        //@include breakpoint($tablet-max-1040)
      .popup--basket &
        @include breakpoint($tablet-max-1040)
          padding: 0 0 12px
          align-items: flex-start
          //justify-content: flex-start
          justify-content: space-between
          position: sticky
          //min-height: 48px
          //min-height: auto
          //height: auto
        &:before
          @include breakpoint($tablet-max-1040)
            content: ''
            width: 100%
            height: 12px
            background: $color-white-primary
            position: absolute
            left: 0
            right: 0
            top: 0
            transform: translate3d(0,-12px,0)
    &--sign-in
      height: calc(100% - 84px)
      @include breakpoint($tablet-max-1040)
        //height: calc(100% - 38px)
    &--profile
      height: calc(100% - 84px)
      @include breakpoint($tablet-max-1040)
        height: 100%
    &--profile-special
      height: calc(100% - 104px)
      @include breakpoint($tablet-max-1040)
        height: 100%
    //&--prodcut-card
    //  @include breakpoint($tablet-max-1040)
    //    height: auto
    &--adaptive-height
      height: calc(100% - 114px)
      @include breakpoint($tablet-max-1040)
        height: calc(100% - 38px)
    &--centered
      margin: auto 0
      height: 100%
      //@include breakpoint($tablet-max-1040)
    &--gradient-top-to-bottom
      &:after
        content: ''
        display: none
        width: 100%
        height: 36px
        background: linear-gradient(180deg, rgba(255,255,255,1) 37%, rgba(255,255,255,0.020045518207282953) 100%)
        position: absolute
        left: 0
        right: 0
        bottom: 0
        transform: translate3d(0, calc(100% - 1px), 0)
      &.popup__row--gradient-visible
        &:after
          @include breakpoint($tablet-max-1040)
            display: block
    &--gradient-bottom-to-top
      &:after
        content: ''
        display: none
        width: 100%
        height: 36px
        background: linear-gradient(0deg, rgba(255,255,255,1) 37%, rgba(255,255,255,0.020045518207282953) 100%)
        position: absolute
        left: 0
        right: 0
        top: 0
        transform: translate3d(0, calc(-100% + 1px), 0)
        @include breakpoint($tablet-max-1040)
          display: block
      &.popup__row--gradient-visible
        &:after
          @include breakpoint($tablet-max-1040)
            display: block
    &--status
      height: calc(100% - 84px)
    &:last-child
      @include breakpoint($tablet-max-1040)
        .popup--basket &
          //min-height: 100%
          //height: auto
          height: 100%
        .popup--basket-empty &
          height: 100%
  &__title
    @include popup-heading-1
    color: $color-gray-primary
    padding: 0 48px
    @include breakpoint($tablet-max-1040)
      padding: 0 24px
  &__btn
    cursor: pointer
    //@include breakpoint($tablet-max-1040)
    &--back
      position: relative
      border-radius: 36px
      background: $color-gray-tertiary
      width: 52px
      height: 36px
      cursor: pointer
      transition: background 0.24s ease-in-out
      @include breakpoint($tablet-max-1040)
        top: 0
      &:after
        content: ''
        @include icon-arrow-black-left
        position: absolute
        @include pos-all-center
        //@include breakpoint($tablet-max-1040)
      &:hover
        background: $color-gray-quinary
        //@include breakpoint($tablet-max-1040)
    &--close
      //position: absolute
      //right: 0
      //top: 24px
      @include icon-close-big($color-gray-tertiary)
      cursor: pointer
      transition: background-image 0.24s ease-in-out
      @include breakpoint($tablet-max-1040)
        top: 0
      &:hover
        @include icon-close-big($color-gray-quinary)
        //@include breakpoint($tablet-max-1040)
      .popup--gallery &
        position: absolute
        z-index: 9999
        right: 56px
        top: 56px
        cursor: pointer
        //@include breakpoint($tablet-max-1040)
        //  right: 20px
        //  top: 74px
    &--hidden
      display: none
      //@include breakpoint($tablet-max-1040)
  &__overlay
    content: ''
    display: block
    width: 100%
    height: 100%
    background: rgba($color-black-secondary,0.8)
    position: fixed
    left: 0
    right: 0
    bottom: 0
    top: 0
    z-index: 9997
    //@include breakpoint($tablet-max-1040)
