input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"]
  user-select: none
  caret-color: $color-red-primary
  //@include breakpoint($tablet-max-1040)
textarea
  user-select: none
  caret-color: $color-red-primary
  //@include breakpoint($tablet-max-1040)
label,
legend
  user-select: none
  display: block
  //@include breakpoint($tablet-max-1040)
fieldset
  padding: 0
  border-width: 0
input[type="checkbox"],
input[type="radio"]
  display: block
  user-select: none
  //@include breakpoint($tablet-max-1040)
select
  user-select: none
  option
    user-select: none
