.section
  //@include breakpoint($tablet-max-1040)
  //&__container
    //@include breakpoint($tablet-max-1040)
  &__row
    margin: 0 0 20px
    @include breakpoint($tablet-max-1040)
      margin: 0 0 18px
      display: flex
      justify-content: space-between
      align-items: center
    &--grid-4-cols
      display: grid
      grid-template-columns: repeat(4, 1fr)
      grid-row-gap: 20px
      grid-column-gap: 8px
      @include breakpoint($desktop-max-1280)
        display: flex
        flex-wrap: nowrap
        justify-content: flex-start
        align-items: flex-start
        column-gap: 12px
        row-gap: 0
        overflow-x: scroll
        scrollbar-width: none
      @include breakpoint($tablet-max-1040)
        display: flex
        flex-wrap: nowrap
        justify-content: flex-start
        align-items: flex-start
        column-gap: 12px
        row-gap: 0
        overflow-x: scroll
        scrollbar-width: none
      &::-webkit-scrollbar
        display: none
    &--grid-5-cols
      display: grid
      grid-template-columns: repeat(5, minmax(0, 1fr))
      grid-row-gap: 20px
      grid-column-gap: 8px
      @include breakpoint($desktop-max-1440)
        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr))
        //grid-template-columns: repeat(4, 1fr)
      @include breakpoint($tablet-max-1040)
        display: flex
        flex-wrap: nowrap
        column-gap: 0
        row-gap: 0
        overflow-x: scroll
        scrollbar-width: none
      @include breakpoint($tablet-max-768)
        row-gap: 6px
        column-gap: 0
      .content__row--tile-mosaic &
        @include breakpoint($tablet-max-1040)
          display: grid
          justify-content: flex-start
          align-items: flex-start
          grid-template-columns: repeat(3, 1fr)
        @include breakpoint($tablet-max-768)
          grid-template-columns: repeat(3, 1fr)
          row-gap: 6px
          column-gap: 6px
        @include breakpoint($mobile-max-640)
          grid-template-columns: repeat(3, 1fr)
      &.section__row--special-mobile-view
        @include breakpoint($tablet-max-1040)
          display: grid
          justify-content: flex-start
          align-items: flex-start
          grid-template-columns: repeat(4, 1fr)
          column-gap: 16px
        @include breakpoint($tablet-max-768)
          grid-template-columns: repeat(3, 1fr)
          column-gap: 12px
        @include breakpoint($mobile-max-640)
          grid-template-columns: repeat(2, 1fr)
          column-gap: 8px
      &.section__row--category
        @include breakpoint($tablet-max-1040)
          display: grid
          justify-content: flex-start
          align-items: flex-start
          grid-template-columns: repeat(5, 1fr)
        @include breakpoint($tablet-max-768)
          grid-template-columns: repeat(4, 1fr)
          row-gap: 6px
          column-gap: 6px
        @include breakpoint($mobile-max-640)
          grid-template-columns: repeat(4, 1fr)
        @include breakpoint($mobile-max-480)
          grid-template-columns: repeat(3, 1fr)
        @include breakpoint($mobile-max-375)
          grid-template-columns: repeat(2, 1fr)
        @include breakpoint($mobile-max-360)
          grid-template-columns: repeat(1, 1fr)
      &::-webkit-scrollbar
        display: none
    &--grid-3-plus-1-cols
      display: grid
      grid-template-columns: repeat(3, 1fr) minmax(200px, 1fr)
      grid-column-gap: 8px
      grid-row-gap: 20px
      align-items: stretch
      @include breakpoint($desktop-max-1440)
        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr))
      @include breakpoint($tablet-max-1040)
        display: flex
        flex-wrap: nowrap
        column-gap: 0
        row-gap: 0
        overflow-x: scroll
        scrollbar-width: none
      &::-webkit-scrollbar
        display: none
        //@include breakpoint($tablet-max-1040)
        //  display: none
    //&--mobile-column
    //  @include breakpoint($mobile-max-480)
    //    flex-direction: column
    //&--special-mobile-view
    &--info
      display: flex
      justify-content: flex-start
      align-items: center
      flex-direction: column
      row-gap: 30px
      padding: 27vh 0
      //@include breakpoint($tablet-max-1040)
    &--mobile-mosaic
      @include breakpoint($tablet-max-1040)
        display: grid
        grid-template-columns: repeat(3, minmax(0, 1fr))
        grid-row-gap: 6px
        grid-column-gap: 6px
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__title
    @include heading-2
    color: $color-gray-quaternary
    //@include breakpoint($tablet-max-1040)
  &__link
    font: 500 13px/1.38 $font-rubik
    letter-spacing: -0.02px
    color: $color-gray-primary
    text-decoration: none
    display: none
    justify-content: flex-start
    align-items: center
    @include breakpoint($tablet-max-1040)
      display: flex
    &:after
      content: ''
      @include icon-arrow-right-with-bg
      margin: 0 0 0 5px
