.promo
  border-radius: 20px
  overflow: hidden
  @include breakpoint($tablet-max-1040)
    border-radius: 0
    padding: 0
    margin: 0 0 24px
  &__carousel
    background: #FAFAFA
    //@include breakpoint($tablet-max-1040)
  &__carousel-item
    width: 76.623376623376623%
    height: 300px
    position: relative
    margin-right: 10px
    @include breakpoint($tablet-max-1040)
      height: calc((100vw - 24px) / 1.957894736842105)
      width: 100%
      margin-right: 0
      //width: 71.098265895953757%
  img
    object-fit: cover
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    //@include breakpoint($tablet-max-1040)
  .flickity-prev-next-button
    background-color: transparent
    will-change: left, right
    user-select: none
    //@include breakpoint($tablet-max-1040)
    &.previous
      @include icon-arrow-white-left-big
      transform-origin: center center
      border-radius: 0
      outline: none
      user-select: none
      border: none
      left: 30px
      transition: left 0.1s ease, opacity 0.2s ease
      @include breakpoint($tablet-max-1040)
        width: calc(22px / 1.3)
        min-width: calc(22px / 1.3)
        height: calc(40px / 1.3)
        min-height: calc(40px / 1.3)
        left: 16px
      &:hover
        left: 28px
        @include breakpoint($tablet-max-1040)
          left: 14px
    &.next
      @include icon-arrow-white-right-big
      right: 30px
      transform-origin: center center
      border-radius: 0
      outline: none
      user-select: none
      border: none
      transition: right 0.1s ease, opacity 0.2s ease
      @include breakpoint($tablet-max-1040)
        width: calc(22px / 1.3)
        min-width: calc(22px / 1.3)
        height: calc(40px / 1.3)
        min-height: calc(40px / 1.3)
        right: 16px
      &:hover
        right: 28px
        @include breakpoint($tablet-max-1040)
          right: 14px
    &:hover
      background-color: transparent
      //@include breakpoint($tablet-max-1040)
    &:active
      opacity: 0.84
      //@include breakpoint($tablet-max-1040)
    .flickity-button-icon
      display: none !important
      visibility: hidden !important
      opacity: 0 !important
      border: none !important
      outline: none !important
      user-select: none !important
      //@include breakpoint($tablet-max-1040)
