.card-category
  background: $color-gray-tertiary
  border-radius: 12px
  overflow: hidden
  min-height: 242px
  display: flex
  justify-content: stretch
  align-items: stretch
  text-decoration: none
  @include breakpoint($tablet-max-1040)
    min-height: 142px
  &__container
    padding: 12px
    width: 100%
    min-height: 100%
    position: relative
    //@include breakpoint($tablet-max-1040)
  h2
    @include preview-category-title
    color: $color-gray-quaternary
    position: relative
    z-index: 2
    //@include breakpoint($tablet-max-1040)
  img
    object-fit: cover
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    //@include breakpoint($tablet-max-1040)
