.attached-button
  min-width: 90px
  position: fixed
  right: 24px
  bottom: 24px
  background: $color-red-primary
  padding: 8px 24px
  border-radius: 56px
  justify-content: center
  align-items: center
  flex-direction: column
  z-index: 9998
  text-decoration: none
  outline: none
  border: none
  cursor: pointer
  will-change: background
  transition: background 0.24s ease
  display: none
  @include breakpoint($tablet-max-1040)
    display: flex
  &:hover
    background: $color-red-secondary
  &__title
    font: 600 16px/19px $font-rubik
    letter-spacing: -.3px
    color: $color-white-primary
  &__descr
    font: 500 11px/13px $font-rubik
    letter-spacing: 0.01px
    color: $color-white-primary
