.profile
  width: 100%
  //@include breakpoint($tablet-max-1040)
  &--main
    height: 100%
    //@include breakpoint($tablet-max-1040)
  &--settings
    height: 100%
    //@include breakpoint($tablet-max-1040)
  &--question
    height: 100%
    //@include breakpoint($tablet-max-1040)
  //&--orders
  &--contact-us
    height: 100%
    //@include breakpoint($tablet-max-1040)
  &__container
    .profile--main &
      display: flex
      flex-direction: column
      justify-content: space-between
      align-items: flex-start
      height: 100%
    //@include breakpoint($tablet-max-1040)
    .profile--settings &
      display: flex
      flex-direction: column
      justify-content: flex-start
      align-items: center
      height: 100%
      //@include breakpoint($tablet-max-1040)
    .profile--contact-us &
      display: flex
      flex-direction: column
      justify-content: space-between
      align-items: flex-start
      width: 100%
      height: 100%
      //@include breakpoint($tablet-max-1040)
    .profile--question &
      display: flex
      flex-direction: column
      justify-content: center
      align-items: center
      width: 100%
      height: 100%
      //@include breakpoint($tablet-max-1040)
  &__header
    width: 100%
    //@include breakpoint($tablet-max-1040)
    &--column
      display: flex
      justify-content: flex-start
      align-items: flex-start
      flex-direction: column
  &__header-title
    font: 600 24px/1 $font-rubik
    color: $color-gray-quaternary
    letter-spacing: -0.4px
    margin: 0 0 4px
    text-align: left
    width: 100%
    @include breakpoint($tablet-max-1040)
      margin: 6px 0 4px
    &--space-around
      padding: 12px 0
      //@include breakpoint($tablet-max-1040)
  &__header-descr
    font: 500 14px/1.71 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-secondary
    margin: 0 0 4px
    text-decoration: none
    -webkit-user-modify: read-write-plaintext-only
    width: 100%
    text-align: left
    @include breakpoint($tablet-max-1040)
      display: block
  //&__header-selected
  //  font: 500 14px/1.71 $font-rubik
  //  letter-spacing: -0.1px
  //  color: $color-orange-primary
  &__header-bonuses
    width: 100%
    background: $color-orange-primary
    background-image: linear-gradient(270deg, #EA5219 1%, #FF6601 39%, #FF7709 98%)
    border-radius: 18px
    padding: 16px 18px
    display: flex
    justify-content: space-between
    align-items: stretch
    column-gap: 16px
    //@include breakpoint($tablet-max-1040)
  &__header-bonuses-col
    width: 50%
    //@include breakpoint($tablet-max-1040)
  &__header-bonuses-title
    font: 600 16px/1.45 $font-rubik
    letter-spacing: 0.01px
    color: $color-white-primary
    //@include breakpoint($tablet-max-1040)
  &__header-bonuses-descr
    font: 400 13px/1.45 $font-rubik
    letter-spacing: 0.08px
    color: $color-white-primary
    //@include breakpoint($tablet-max-1040)
  &__nav
    width: 100%
    margin-top: 12px
    margin-bottom: auto
    //@include breakpoint($tablet-max-1040)
    &--small
      margin-bottom: 64px
      //@include breakpoint($tablet-max-1040)
  &__nav-item
    width: 100%
    padding: 16px 0
    text-decoration: none
    display: flex
    justify-content: flex-start
    align-items: center
    column-gap: 10px
    border-bottom: 1px solid $color-gray-quinary
    cursor: pointer
    //@include breakpoint($tablet-max-1040)
    .profile__nav--small &
      column-gap: 12px
      padding: 12px 0
      //@include breakpoint($tablet-max-1040)
    &--rows
      align-items: flex-start
      justify-content: center
      flex-direction: column
      //@include breakpoint($tablet-max-1040)
    @include hover
    &:first-child
      border-top: 1px solid $color-gray-quinary
      //@include breakpoint($tablet-max-1040)
    span
      cursor: pointer
      //@include breakpoint($tablet-max-1040)
      &:first-child
        font: 500 32px/1 $font-rubik
        letter-spacing: -0.32px
        //@include breakpoint($tablet-max-1040)
        .profile__nav--small &
          font: 500 16px/1.5 $font-rubik
          letter-spacing: -0.1px
          //@include breakpoint($tablet-max-1040)
      &:last-child
        font: 500 20px/1.2 $font-rubik
        color: $color-black-secondary
        letter-spacing: -0.32px
        //@include breakpoint($tablet-max-1040)
        .profile__nav--small &
          font: 500 16px/1.5 $font-rubik
          letter-spacing: -0.1px
          color: $color-gray-quaternary
          //@include breakpoint($tablet-max-1040)
  &__nav-descr
    font: 500 14px/1.14 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-secondary
    //@include breakpoint($tablet-max-1040)
  &__btn
    font: 500 16px/1.18 $font-rubik
    color: $color-red-primary
    letter-spacing: -0.05px
    display: flex
    justify-content: center
    align-items: center
    cursor: pointer
    text-decoration: none
    @include hover
    //@include breakpoint($tablet-max-1040)
    &--remove-account
      margin-top: auto
      margin-bottom: 12px
  .button
    max-width: 304px
    margin: 0 auto 16px
    display: block
    //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__input-simple-wrapper
    width: 100%
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    margin: auto 0 auto
    row-gap: 32px
    //@include breakpoint($tablet-max-1040)
    .button
      width: auto
      min-width: 295px
      //@include breakpoint($tablet-max-1040)
  &__input-simple-row
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: center
    //@include breakpoint($tablet-max-1040)
  &__input-simple-title
    @include login-title
    color: $color-gray-secondary
    margin: 0 0 6px
    //@include breakpoint($tablet-max-1040)
  &__input-simple-input
    @include secondary-text-input
    margin: 0 0 16px
    @include breakpoint($tablet-max-1040)
      margin-left: auto
      margin-right: auto
      width: 90%
    &:last-child
      margin-bottom: 0
  // list
  &__order-list
    margin: 0 0 32px
    //@include breakpoint($tablet-max-1040)
  &__order-title
    margin: 0 0 12px
    //@include breakpoint($tablet-max-1040)
    span
      &:first-child
        font: 600 24px/1 $font-rubik
        letter-spacing: -0.4px
        color: $color-gray-quaternary
        //@include breakpoint($tablet-max-1040)
      &:last-child
        font: 600 24px/1 $font-rubik
        letter-spacing: -0.4px
        color: $color-gray-secondary
        margin: 0 0 0 6px
        //@include breakpoint($tablet-max-1040)
  &__order-item
    display: flex
    justify-content: flex-start
    align-items: stretch
    column-gap: 16px
    position: relative
    text-decoration: none
    padding: 12px 0
    border-bottom: 1px solid $color-gray-quinary
    width: 100%
    transition: opacity 0.24s ease
    will-change: opacity
    @include breakpoint($tablet-max-1040)
      column-gap: 12px
    &:first-child
      border-top: 1px solid $color-gray-quinary
      //@include breakpoint($tablet-max-1040)
    &:after
      content: ''
      @include icon-arrow-top-simple
      transform: translate3d(0, -50%, 0) rotate(90deg)
      top: 50%
      right: 0
      position: absolute
      //@include breakpoint($tablet-max-1040)
    &:hover:after
      opacity: 0.84
      //@include breakpoint($tablet-max-1040)
  &__order-item-col
    &--left
      display: grid
      grid-template-columns: 1fr 1fr
      grid-gap: 1px
      width: 80px
      height: 80px
      border-radius: 12px
      overflow: hidden
      background: $color-white-primary
      //@include breakpoint($tablet-max-1040)
      @include breakpoint($mobile-max-480)
        width: 60px
        height: 60px
        border-radius: 8px
    &--right
      display: flex
      flex-direction: column
      justify-content: space-between
      align-items: flex-start
      //@include breakpoint($tablet-max-1040)
  &__order-item-img-wrapper
    position: relative
    background: $color-gray-tertiary
    width: calc((80px - 3px) / 2)
    height: calc((80px - 3px) / 2)
    @include breakpoint($tablet-max-1040)
      width: calc((60px - 3px) / 2)
      height: calc((60px - 3px) / 2)
  &__order-item-img
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    object-fit: cover
    //@include breakpoint($tablet-max-1040)
  &__order-item-title
    font: 600 18px/1.22 $font-rubik
    letter-spacing: -0.32px
    color: $color-gray-quaternary
    display: flex
    justify-content: flex-start
    align-items: center
    column-gap: 6px
    transition: color 0.24s ease
    will-change: color
    //@include breakpoint($tablet-max-1040)
    @include breakpoint($mobile-max-480)
      font-weight: 500
    //&--fulfilled
    //&--cancelled
    //&--awaiting-payment
    //&--payment-error
    //&--awaiting-confirmation
    .profile__order-item:hover &
      color: rgba($color-gray-quaternary,0.75)
      //@include breakpoint($tablet-max-1040)
  &__order-item-title-icon
    width: 20px
    height: 20px
    border-radius: 10px
    display: flex
    justify-content: center
    align-items: center
    @include breakpoint($tablet-max-1040)
      width: 18px
      height: 18px
    &:after
      content: ''
      //@include breakpoint($tablet-max-1040)
    .profile__order-item-title--fulfilled &
      background: $color-green-primary
      &:after
        @include icon-checkbox
        width: 10px
        min-width: 10px
        height: 10px
        min-height: 10px
        transform: translate3d(0.3px,0,0)
        @include breakpoint($tablet-max-1040)
          width: 9px
          min-width: 9px
          height: 9px
          min-height: 9px
    .profile__order-item-title--cancelled &
      background: $color-red-primary
      &:after
        @include icon-close($color-white-primary)
        width: 8px
        min-width: 8px
        height: 8px
        min-height: 8px
        //@include breakpoint($tablet-max-1040)
    .profile__order-item-title--awaiting-payment &
      background: $color-orange-primary
      &:after
        @include icon-attention($color-orange-primary)
        width: 20px
        min-width: 20px
        height: 20px
        min-height: 20px
        @include breakpoint($tablet-max-1040)
          width: 18px
          min-width: 18px
          height: 18px
          min-height: 18px
    .profile__order-item-title--payment-error &
      background: $color-red-primary
      &:after
        @include icon-close($color-white-primary)
        width: 8px
        min-width: 8px
        height: 8px
        min-height: 8px
        //@include breakpoint($tablet-max-1040)
    .profile__order-item-title--awaiting-confirmation &
      background: $color-gray-secondary
      &:after
        @include icon-attention($color-gray-secondary)
        width: 20px
        min-width: 20px
        height: 20px
        min-height: 20px
        @include breakpoint($tablet-max-1040)
          width: 18px
          min-width: 18px
          height: 18px
          min-height: 18px
  &__order-item-descr
    span
      font: 500 14px/1.28 $font-rubik
      letter-spacing: -0.1px
      color: $color-gray-secondary
      @include breakpoint($tablet-max-1040)
        font-size: 13px
      //&:first-child
      &:last-child
        &:before
          content: '•'
          font: 500 14px/1.28 $font-rubik
          letter-spacing: -0.1px
          color: $color-gray-secondary
          margin: 0 6px
          //@include breakpoint($tablet-max-1040)
  // links
  &__links-wrapper
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: center
    row-gap: 32px
    width: 100%
    padding: 0
    margin-top: auto
    margin-bottom: auto
  &__link
    font: 500 20px/1.2 $font-rubik
    letter-spacing: -0.32px
    color: $color-gray-quaternary
    display: flex
    justify-content: center
    align-items: center
    column-gap: 12px
    text-decoration: none
    @include breakpoint($tablet-max-1040)
      font-size: 18px
      column-gap: 10px
    &:after
      content: ''
      @include icon-arrow-white-right-top
      transition: opacity 0.24s ease
      //@include breakpoint($tablet-max-1040)
    &:hover:after
      opacity: 0.7
      //@include breakpoint($tablet-max-1040)
  // text input
  &__feedback-form
    width: 100%
    //@include breakpoint($tablet-max-1040)
  &__feedback-form-row
    width: 100%
    margin: 0 0 24px
    //@include breakpoint($tablet-max-1040)
    &:last-child
      margin-bottom: 0
      //@include breakpoint($tablet-max-1040)
  &__feedback-form-label
    font: 500 12px/1.18 $font-rubik
    color: $color-gray-secondary
    letter-spacing: -0.32px
    position: relative
    display: flex
    justify-content: flex-start
    align-items: center
    margin: 0 0 6px
    //@include breakpoint($tablet-max-1040)
  &__feedback-form-input-text-wrapper
    position: relative
    //@include breakpoint($tablet-max-1040)
  &__feedback-form-input-text
    width: 100%
    background: transparent
    border: none
    border-bottom: 1px solid $color-gray-quinary
    outline: none
    padding: 0 0 5px
    color: $color-black-secondary
    text-align: left
    font-family: $font-rubik
    font-size: 18px
    font-style: normal
    font-weight: 500
    line-height: 1
    letter-spacing: -0.32px
    z-index: 1
    transition: color 0.3s ease, border-color 0.3s ease
    //@include breakpoint($tablet-max-1040)
    @include placeholder
      color: $color-gray-quinary
      transition: color 0.3s ease, border-color 0.3s ease
      //@include breakpoint($tablet-max-1040)
  &__footer
    width: calc(100% + 64px)
    transform: translate3d(-32px, 32px, 0)
    padding: 32px 24px
    border-radius: 0 0 32px 32px
    background: $color-gray-tertiary
    display: flex
    justify-content: center
    align-items: center
    text-align: center
    font: 500 14px/1.28 $font-rubik
    letter-spacing: -0.1px
    color: $color-gray-secondary
    @include breakpoint($tablet-max-1040)
      transform: translate3d(-32px, 12px, 0)
      display: none
  // Question
  &__question
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    row-gap: 32px
    width: 100%
    height: 100%
    //@include breakpoint($tablet-max-1040)
  &__question-row
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    width: 100%
    max-width: 304px
    //@include breakpoint($tablet-max-1040)
  &__question-title
    font: 600 20px/1.2 $font-rubik
    letter-spacing: -0.3px
    color: $color-gray-primary
    //@include breakpoint($tablet-max-1040)
  // checkbox
  &__input-checkbox
    @include checkbox-default
    margin: 0 32px 0 auto
    //@include breakpoint($tablet-max-1040)
    .profile__nav-item--rows &
      transform: translate3d(0,-20px,0)
      //@include breakpoint($tablet-max-1040)
