.card-more
  border-radius: 16px
  overflow: hidden
  min-height: 304px
  display: flex
  justify-content: stretch
  align-items: stretch
  text-decoration: none
  position: relative
  @include breakpoint($tablet-max-1040)
    width: 262px
    min-height: 80px
  &:after
    content: ''
    display: block
    width: 100%
    height: 100%
    position: absolute
    left: 0
    top: 0
    bottom: 0
    right: 0
    z-index: 2
    background: $color-gray-quaternary
    opacity: 0.3
    transition: opacity 0.24s ease-in-out
    //@include breakpoint($tablet-max-1040)
  &:hover
    &:after
      opacity: 0.4
      //@include breakpoint($tablet-max-1040)
  &__container
    padding: 12px
    width: 100%
    min-height: 100%
    position: relative
    //@include breakpoint($tablet-max-1040)
  .button
    @include pos-all-center
    z-index: 3
    width: auto
    //@include breakpoint($tablet-max-1040)
  img
    object-fit: cover
    width: 100%
    height: 100%
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 1
    //@include breakpoint($tablet-max-1040)
