.nav
  display: grid
  grid-template-columns: 1fr
  grid-auto-rows: minmax(min-content, max-content)
  overflow-y: scroll
  scrollbar-color: transparent transparent
  height: 100%
  padding: 12px 0
  scrollbar-width: none
  //@include breakpoint($tablet-max-1040)
  &::-webkit-scrollbar
    display: none
    //@include breakpoint($tablet-max-1040)
  &__item
    display: flex
    justify-content: flex-start
    align-items: center
    column-gap: 8px
    text-decoration: none
    padding: 8px 0
    cursor: pointer
    //@include breakpoint($tablet-max-1040)
  &__icon
    width: 32px
    height: 32px
    display: flex
    justify-content: center
    align-items: center
    border-radius: 6px
    background-color: $color-gray-tertiary
    position: relative
    transition: background 0.15s ease-in-out
    //@include breakpoint($tablet-max-1040)
    .nav__item:hover &
      background: rgba($color-gray-senary,0.4)
      //@include breakpoint($tablet-max-1040)
    .nav__item--active:hover &
      text-indent: -9999px
      &:after
        content: ''
        @include icon-arrow-top-simple
        position: absolute
        left: 50%
        top: 50%
        transform: translate3d(-50%, calc(-50% - 1px), 0)
        //@include breakpoint($tablet-max-1040)
    img
      width: 18px
      min-width: 18px
      height: 18px
      min-height: 18px
      display: block
      opacity: 1
      will-change: opacity
      transition: opacity 0.15s ease
      .nav__item--active:hover &
        opacity: 0
  &__text
    @include sidebar
    color: $color-gray-primary
    //@include breakpoint($tablet-max-1040)
  &__submenu
    justify-content: flex-start
    align-items: flex-start
    flex-direction: column
    row-gap: 14px
    padding-top: 4px
    padding-bottom: 16px
    display: none
    opacity: 0
    transition: opacity 0.4s ease, display 0s ease
    //@include breakpoint($tablet-max-1040)
    &--active
      opacity: 1
      display: flex
      //@include breakpoint($tablet-max-1040)
  &__submenu-item
    font: 500 14px/1.285 $font-rubik
    color: $color-gray-primary
    letter-spacing: -.1px
    padding: 0 0 0 40px
    text-decoration: none
    transition: color 0.24s ease
    //@include breakpoint($tablet-max-1040)
    &:hover,
    &--active
      color: $color-gray-secondary
      //@include breakpoint($tablet-max-1040)
